<script>
import { Rate , Popup , Toast , Icon , Overlay , PullRefresh , NoticeBar } from 'vant';
import Swiper from 'swiper'
import 'swiper/css/swiper.min.css'
import 'swiper/js/swiper.min.js'
import selfIcon from '@/components/self-icon'
import Order from "@/common/Order.json"

export default {
	components: { Rate , Popup , Icon , Overlay , selfIcon , PullRefresh , NoticeBar },
	data () {
		return {
			value: 1,
			active: 1,
			ProvideHelpList: [],
			times: '',
			totalOut: 0,
			totalIn: 0,
			lucky: false,
			luckyAmout: '',
			yubei: 0,
			baowu: 0,
			jiangchi: 0,
			huodong: 0,
			isInCountdowning: false,
			name: '',
			deadlineTimes: '00:00:00',
			totalAddresses: 0,
			ranshao: 0,
			language: 0,
			currentTime: 0,
			theme: localStorage.getItem('theme'),
			isLoading: false,
			count: 0,
		}
	},
	methods:{
		switchLanguage(type) {
			localStorage.setItem('locale',type)
			this.$i18n.locale = type
		},
		async init() {
			// this.$tronWeb.call({
			// 	contractAddress: this.$contracts.Manager.address,
			// 	method: 'calculationAwards(address,uint256)',
			// 	parameter: [
			// 		{ type: 'address', value: 'TMLoqfUYiDx8NwQKhqRYDfi7RttxxLSEhV' },
			// 		{ type: 'uint256', value: '100'.toSun() },
			// 	]
			// }).then(res => {
			// 	const { addresses, awards } = res.constant_result[0].decode(['address[]', 'uint256[]'], ['addresses', 'awards']);
			// 	console.log(addresses);
			// 	console.log(addresses.map(item => window.tronWeb.address.fromHex(item)));
			// 	console.log(awards.map(item => item.toString().fromSun()));
			// 	console.log(addresses.map(async item => await this.$tronWeb.call({
			// 		contractAddress: this.$contracts.Relations.address,
			// 		method: 'addressToNickName(address)',
			// 		parameter: [{ type: "address", value: item }]
			// 	}).then(NickName => {
			// 		return NickName.constant_result[0].decode(['bytes16']).hexToString(true);
			// 	})))
			// })

			//幸运池
			this.$contracts.Pool.availTotalAmouns(0).call().then(yubei => {
				this.jiangchi = Math.floor(yubei.toString().fromSun());
			})
			
			//探索基金
			this.$contracts.Pool.availTotalAmouns(1).call().then(luckPool => {
				this.yubei = Math.floor(luckPool.toString().fromSun());
			})

			//宝物基金
			this.$contracts.Pool.availTotalAmouns(2).call().then(baowu => {
				this.baowu = Math.floor(baowu.toString().fromSun());
			})

			//累计提供帮助/累计获得帮助
			this.$tronWeb.call({
				contractAddress: this.$contracts.OrderManager.address,
				method: 'userInfoMapping(address)',
				parameter: [{ type: "address", value: this.$address }]
			}).then(UserInfo => {
				const { totalIn, totalOut } = UserInfo.constant_result[0].decode(
					['uint256', 'uint256', 'uint256', 'uint256'],
					['totalIn', 'totalOut', 'awardQuota', 'awardWithdrawableTotal']
				);

				this.totalIn = totalIn.toString().fromSun();
				this.totalOut = totalOut.toString().fromSun();
			});
			// this.$tronWeb.call({
			// 	contractAddress: this.$contracts.phoenix.address,
			// 	method: 'GetInoutTotalInfo(address)',
			// 	parameter: [{ type: "address", value: this.$address }]
			// }).then(TotalInfo => {
			// 	const totalInfo = TotalInfo.constant_result[0].decode(['uint256', 'uint256'], ['totalIn', 'totalOut']);
			// 	this.totalOut = totalInfo.totalOut.toString().fromSun();
			// 	this.totalIn = totalInfo.totalIn.toString().fromSun();
			// })

			//地址总量
			this.$contracts.Relations.totalAddresses().call().then(Addresses => {
				this.totalAddresses = Addresses.toString();
			})

			//燃烧DT
			this.$contracts.ERC777_1Token.balanceOf('0x0000000000000000000000000000000000000000').call().then(Balance => {
				this.ranshao = Math.floor(Balance.toString().fromSun());
			})

			let currentTime = await this.$tronWeb.getBlockTime();

			//是否需要暂停
			this.$tronWeb.call({
				contractAddress: this.$contracts.OrderManager.address,
				method: 'deadlineTime()',
				issuerAddress: this.$address,
				parameter: []
			}).then(res => {
				if(res.constant_result[0].decode(['uint256']).toNumber() && currentTime > res.constant_result[0].decode(['uint256']).toNumber()){
					// 是否需要暂停
					this.$tronWeb.call({
						contractAddress: this.$contracts.OrderManager.address,
						method: 'isLuckDog(address)',
						issuerAddress: this.$address,
						parameter: [{ type: "address", value: this.$address }]
					}).then(async res => {
						const IsLuckDog = res.constant_result[0].decode(['bool','uint256','bool'],['isluckDog','award','canwithdrawable'])

						if (IsLuckDog.isluckDog && IsLuckDog.canwithdrawable) {
							this.lucky = true;
							this.luckyAmout = IsLuckDog.award.toString().fromSun().toFixed(2);
						} else {
							this.lucky = false;
							const death = await this.$contracts.OrderManager.death().call();
							if(death){
								this.$router.replace('/drawCompensate')
							}
						}
					})
				}

				this.deadlineTime = res.constant_result[0].decode(['uint256']).toNumber();
						
				if(currentTime < this.deadlineTime){
					var deadlineTime = setInterval(()=>{
						currentTime++;
						this.deadlineTimes = this.timeDown(
							this.deadlineTime,
							currentTime
						);
					}, 1000);
				}
			})

			//是否开启倒计时
			this.$contracts.OrderManager.isInCountdowning().call((err,result)=>{
				this.isInCountdowning = result
			})

			//获取名字
			this.$tronWeb.call({
				contractAddress: this.$contracts.Relations.address,
				method: 'addressToNickName(address)',
				parameter: [{ type: "address", value: this.$address }]
			}).then(NickName => {
				const name = NickName.constant_result[0].decode(['bytes16']);
				this.name = name.hexToString(true);
			})

			this.QueryOrders(0, 255, 0, 10).then(({ total }) => {
				const totalNum = total.toNumber();

				if (totalNum !== this.ProvideHelpList.length) {
					this.ProvideHelpList = [];
					
					const maxOffset = totalNum <= 10 ? 0 : Number(total.toString().substr(0, 1)) * 10;
					const promises = [];

					for (let i = maxOffset; i >= 0; i -= 10) {
						promises.push(this.QueryOrders(0, 255, i, 10));
					}
					Promise.all(promises).then(async QueryOrders => {
						for (const { len, states, totalAmounts, times , id , total } of QueryOrders) {
							let currentTime = 0;
							for (let i = len.toNumber() - 1; i >= 0; i--) {
								const orderState = states[i];
								
								if ([2,5,8].includes(orderState)) {
									const totalAmount = totalAmounts[i].toString().fromSun();

									if([2].includes(orderState)){
										var currentScale = 50
									}else{
										var currentScale = 100;
									}

									currentTime = await this.$tronWeb.getBlockTime();
									const orderTimes = (await this.$tronWeb.call({
										contractAddress: this.$contracts.OrderManager.address,
										method: 'orderTimes(uint256)',
										parameter: [{ type: "uint256", value: id[i] }]
									}).then()).constant_result[0].decode(
										['uint256','uint256','uint256','uint256','uint256','uint256','uint256','uint256','uint256'],
										['OnCreated','OnPaymentFrist','OnPaymentSuccess','OnProfitingBegin','OnCountDownStart','OnCountDownEnd','OnConvertConsumer','OnUnfreezing','OnDone']
									)

									const OnCreated = orderTimes.OnCreated.toNumber()
									const OnCountDownStrat = orderTimes.OnCountDownStart.toNumber()
									const OnCountDownEnd = orderTimes.OnCountDownEnd.toNumber()
									const CurrentProfitInfoTime = orderTimes.OnUnfreezing.toNumber()
			
									this.ProvideHelpList.push({
										order: id[i],
										totalAmount: totalAmount,
										state: orderState,
										cretaTime: OnCreated,
										timesDown: '00:00:00:00',
										OnCountDownEnd: OnCountDownEnd,
										CurrentProfitInfo: (CurrentProfitInfoTime && currentTime >= CurrentProfitInfoTime),
										OrderCountdownStarts: (currentTime > OnCountDownStrat && orderState == 2),
										total: total.toNumber(),
										currentScale: currentScale,
									});
								}
							}

							this.times = setInterval(() => {
								currentTime++;
								this.ProvideHelpList.forEach((item, index) => {
									if (item.state == 2) {
										if (currentTime === 0 || currentTime > item.OnCountDownEnd) {
											this.$set(this.ProvideHelpList[index], 'timesDown', '00:00:00:00');
											return clearInterval(this.times);
										} else {
											const timesDown = this.timeDown(item.OnCountDownEnd, currentTime);
											this.$set(this.ProvideHelpList[index], 'timesDown', timesDown);
										}
									}
								})
							}, 1000)
						}
					}).catch(console.log)
				}
			}).catch(console.log)
		},
		async QueryOrders(orderType, orderState, offset, size, address = this.$address) {
			const order = await this.$tronWeb.call({
				contractAddress: this.$contracts.OrderManager.address,
				method: 'selectOrders(address,uint256,uint256,uint256)',
				parameter: [
					{ type: "address", value: this.$address },
					{ type: "uint256", value: orderState },
					{ type: "uint256", value: offset },
					{ type: "uint256", value: size }
				],
			});
		
			return order.constant_result[0].decode(
				['uint256', 'uint256', 'uint256[]', 'uint256[]', 'uint8[]', 'uint96[]'],
				['total', 'len', 'id', 'totalAmounts', 'states', 'times']
			);
		},
		timeDown(endDateStr,currentTime){
			function add(m){return m<10?'0'+m:m }
			//结束时间
			var endDate = endDateStr;
			//当前时间
			var nowDate = currentTime;
			//相差的总秒数
			var totalSeconds = parseInt((endDate - nowDate))
			//天数
			var day=parseInt(totalSeconds/3600/24);
			//取模（余数）
			var modulo = totalSeconds % (60 * 60 * 24);
				
			//小时数
			var hours = Math.floor((modulo / (60 * 60)));
			modulo = modulo % (60 * 60);
			//分钟
			var minutes = Math.floor(modulo / 60);
			//秒
			var seconds = modulo % 60;
			return add(hours+(day*24)) + ":" + add(minutes) + ":" + add(seconds);
		},
		WithdrawLuckAward() {
			Toast.loading({
				message: '正在交易中...',
				forbidClick: true,
				duration: 0
			});

			this.$tronWeb.call({
				contractAddress: this.$contracts.OrderManager.address,
				method: 'withdrawLuckAward()'
			}).then(WithdrawLuckAward => {
				this.$contracts.OrderManager.withdrawLuckAward()
				.send({ feeLimit: '50'.toSun() }).then(hex => {
					sessionStorage.setItem('transactionHex', hex);

					const interval = setInterval(() => {
						window.tronWeb.trx.getTransaction(hex).then(transaction => {
							if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
								sessionStorage.removeItem('transactionHex');
								clearInterval(interval);

								Toast.clear();
								this.init();
								this.$router.replace('/drawCompensate');
							}
						})
					}, 2000);
				})

			}).catch(err => {
				Toast.clear();
				console.log(err);
			});
		},
		zanweikaifang(){
			Toast.fail(this.$t('NotYetOpen'))
		},
		onRefresh() {
		    setTimeout(() => {
		        this.init()
		        Toast('刷新成功');
		        this.isLoading = false;
		    }, 1000);
		},
	},
	mounted() {
		this.$tronWeb && this.init();

		if (this.$tronWeb) {
			this.$tronWeb.call({
				contractAddress: this.$contracts.ERC20Token.address,
				method: 'balanceOf(address)',
				parameter: [{ type: 'address', value: this.$contracts.Pool.address }]
			}).then(Balance => {
				console.log('Pool余额：', Balance.constant_result[0].decode(['uint256']).toString().fromSun());
			})

			console.log('USDT合约地址：', this.$contracts.ERC20Token.address);
		}

		new Swiper('.home-index>.swiper-container', {
			direction: 'horizontal',
			loop: true,
			autoHeight: true,
			autoplay: true
		})

		new Swiper('.home-index>.info>.swiper-container', {
			direction: 'horizontal',
			loop: true,
			autoHeight: true,
			pagination: {
				el: '.swiper-pagination'
			},
			spaceBetween: 15,
			slidesPerView: 3
		})

		// var mySwiper = new Swiper('.swiper-container', {
		// 	direction: 'horizontal', // 垂直切换选项
		// 	loop: true, // 循环模式选项
		// 	// 如果需要分页器
		// 	// pagination: {
		// 	// 	el: '.swiper-pagination',
		// 	// },
		// 	// slidesPerView : 3,
		// 	autoHeight: true, //高度随内容变化
		// })
	},
	destroyed() {
		clearInterval(this.times);
	}
}
</script>
