<template>
	<div class="box">
		<nav-bar :title="title"></nav-bar>
		<div class="navBox cardBox">
			<div>
				<img src="@/assets/cardLogo.png"/>
				<span>{{name}}</span>
				<span>{{code}}</span>
				<span>{{$t('inviterCardText02')}}</span>
				<div class="line">
					<i></i>
				</div>
				<div class="cardBottom">
					<div class="qrcode" v-lazy:background="require('@/assets/qrcode_box.png')">
						<div></div>
					</div>
					<span>{{$t('inviterCardText03')}}</span>
				</div>
			</div>
			<div>
				<!-- <Button @click="save" color="transparent">{{$t('SaveCard')}}</Button> -->
				<!-- <i></i> -->
				<Button
				  color="transparent"
				  v-clipboard:copy="link"
				  v-clipboard:success="onCopySuccess"
				  v-clipboard:error="onCopyError"
				>{{$t('copyLink')}}</Button>
			</div>
		</div>
	</div>
</template>

<script>
	import { Button, Toast } from 'vant'
	import QRCode from 'qrcodejs2'
	// import html2canvas from 'html2canvas'
	import navBar from '@/components/nav-bar'
	
	export default {
		components: { navBar, Button },
		data() {
			return {
				title: this.$t('invite'),
				link: '',
				code: '000000',
				name: '****',
				qrcodeBox: {}
			}
		},
		created() {
			this.$tronWeb && this.init();
		},
		mounted() {
			this.qrcodeBox = document.querySelector('.qrcode>div');
			new QRCode(this.qrcodeBox, `${location.href.slice(0, location.href.indexOf('#'))}#/download`);
			this.qrcodeBox.children.forEach(item => {
				item.style.width = '100%'
				item.style.height = '100%'
			})
		},
		methods: {
			// save() {
			// 	const a = document.createElement('a')
			// 	a.download = 'invitation_card'
			// 	html2canvas(document.querySelector('.navBox>div')).then(canvas => {
			// 		a.href = canvas.toDataURL('image/png')
			// 		a.click()
			// 	})
			// },
			onCopySuccess() {
				Toast.success(this.$t('CopySuccessful'))
			},
			onCopyError() {
				Toast.fail(this.$t('CopyFailed'))
			},
			init() {
				Promise.all([
					this.$tronWeb.call({
						contractAddress: this.$contracts.Relations.address,
						method: 'addressToNickName(address)',
						parameter: [{ type: "address", value: this.$address }]
					}).then(res => res.constant_result[0].decode(['bytes16']).hexToString(true)),
					this.$tronWeb.call({
						contractAddress: this.$contracts.Relations.address,
						method: 'addressToShortCode(address)',
						parameter: [{ type: "address", value: this.$address }]
					}).then(res => res.constant_result[0].decode(['bytes6']).hexToString())
				]).then(([ NickName, ShortCode ]) => {
					this.name = NickName;
					this.code = ShortCode.length > 0 ? ShortCode : '******';

					//生成二维码
					this.link = `${location.href.slice(0, location.href.indexOf('#'))}#/download?code=${this.code}&name=${this.name}`;
					
					this.qrcodeBox.innerHTML = '';
					new QRCode(this.qrcodeBox, this.link);
					this.qrcodeBox.children.forEach(item => {
						item.style.width = '100%';
						item.style.height = '100%';
					});
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.navBox {
		padding: 40px 38px 0;

		&>div:last-child {
			// width: 590px;
			// height: 96px;
			display: flex;
			align-items: center;
			justify-content: center;
			overflow: hidden;
			margin: 58px auto 0;
			&>button {
				background-image: linear-gradient(44deg, #21C17A, #21C1A8)!important;
				width: calc(50% - 1px);
				height: 96px;
				font-size: 40px;
				line-height: 96px;
				border-radius: 50px;
			}
			&>i {
				width: 2px;
				height: 76px;
				background-color: #18222F;
			}
		}
		&>div:first-child {
			background-color: #FFFFFF;
			// color: #8FACD2;
			padding: 114px 0 22px;
			background-size: 100% 100% !important;
			background-repeat: no-repeat !important;
			display: flex;
			align-items: center;
			flex-direction: column;
			border-radius: 20px;
			box-shadow: 0 4px 4px rgba(#000, 0.3);
			&>img{
				width: 228px;
				margin-bottom: 26px;
			}
			&>.line {
				width: 100%;
				height: 60px;
				padding: 0 47px;
				display: flex;
				align-items: center;
				position: relative;
				// overflow: hidden;
				&>i {
					display: block;
					width: 100%;
					border-bottom: 2px dashed #3F4047;
				}
				&::before {
					content: '';
					width: 60px;
					height: 60px;
					position: absolute;
					left: -30px;
					border-radius: 30px;
					background-color: #FCF9EC;
				}
				&::after {
					content: '';
					width: 60px;
					height: 60px;
					position: absolute;
					right: -30px;
					border-radius: 30px;
					background-color: #FCF9EC;
				}
			}
			&>span {
				font-size: 50px;
				color: #666;
				&:nth-of-type(2) {
					color: #000;
					margin-top: 14px;
					margin-bottom: 16px;
					font-size: 90px;
				}
				&:nth-of-type(3) {
					color: #454240;
					font-size: 30px;
					margin-bottom: 160px;
				}
				// &:nth-of-type(4) {
				// 	font-size: 30px;
				// 	margin-top: 2px;
				// 	margin-bottom: 170px;
				// }
				
			}

			&>.cardBottom{
				width: 100%;
				background-image: url('../../../assets/cardBg.png');
				background-size: 100% 332px;
				background-position: bottom;

				&>.qrcode {
					width: 238px;
					height: 239px;
					margin: 24px auto 22px;
					padding: 12px;
					padding-top: 14px;
					background-size: 100% 100% !important;
					background-repeat: no-repeat !important;
					&>div {
						width: 100%;
						height: 100%;
						padding: 9px;
						border-radius: 6px;
						background-color: #fff;
					}
				}

				&>span:last-child {
					font-size: 24px;
				}
			}
		}
	}
</style>
