<template>
	<div class="box">
		<nav-bar :title="title"></nav-bar>
		<div class="navBox bgColor273446 luckPlayerBox">
			<div class="item" v-for="(item, index) in list" :key="index">
				<div>
					<span>No.{{ item.name }}</span>
					<span>{{ `${item.address.slice(0, 7)}...${item.address.slice(-7)}` }}</span>
				</div>
				<span>{{ item.amount }} <span>USDT</span></span>
			</div>
			<not-record v-if="!list.length" :text="NoLuckyPlayers"></not-record>
		</div>
	</div>
</template>

<script>
	import navBar from '@/components/nav-bar'
	import notRecord from '@/components/not-record'
	
	export default {
		components: { navBar, notRecord },
		data() {
			return {
				list: [],
				title: this.$t('LuckyPlayer'),
				NoLuckyPlayers: this.$t('NoLuckyPlayers'),
			}
		},
		mounted() {
			this.$tronWeb && this.$tronWeb.event({
				contractAddress: this.$contracts.OrderManager.address,
				method: 'Log_Luckdog',
				size: 600
			}).then(events => {
				if(!events.length) return

				events.forEach(async event => {
					// const name = await this.$tronWeb.call({
					// 	contractAddress: this.$contracts.Relations.address,
					// 	method: 'addressToNickName(address)',
					// 	parameter: [{ type: 'address', value: event.result.who }]
					// }).then(res => res.constant_result[0].decode(['bytes16']));
		
					this.list.push({
						name: event.result.seqNo,
						address: window.tronWeb.address.fromHex(event.result.who),
						amount: event.result.awardsTotal.fromSun()
					})

					this.list.sort((a, b) => {
						return a.name - b.name;
					})
				})
			})
		}
	}
</script>

<style lang="scss" scoped>
	.item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		// color: #8FACD2;
		font-size: 32px;
		margin-bottom: 30px;
		padding-bottom: 20px;
		border-bottom: 1px solid #516A89;
		&>span>span {
			font-size: 20px;
		}
		&>div {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			&>span:last-child {
				// color: #7489A5;
				font-size: 28px;
				margin-top: 10px;
			}
		}
	}
	.navBox {
		padding: 38px 20px 0;
		// background-color: #273446;
	}
</style>
