<template>
	<div class="box van-tabbar-page">
		<!-- <nav-bar :title="title"></nav-bar> -->
		<div class="exchangeBox">
			<div class="exchangeInputBox">
				<div class="exchangeTitle">{{$t('SwapPeriod')}}：{{roundID}}</div>
				<div class="exchangeProp">
					<span>1:{{prop}}</span>
					<span>{{Remaining}} MPC</span>
				</div>
				<div class="exchangeSchedule">
					<div :style="'width:'+RemainingProp+'%'">
						<div>{{RemainingProp}}%</div>
					</div>
				</div>
				<div class="exchangeInput">
					<input type="number" :placeholder="$t('Send')" v-model="value" @input="input" />
					<div>
						<img src="../../../assets/usdtLogo.png" />
						<span>USDT</span>
					</div>
				</div>
				<div class="exchangeBalance">
					{{$t('Available')}} {{usdt}} USDT
				</div>
				<div class="exchangeInput">
					<input type="number" :placeholder="$t('Received')" disabled="disabled" v-model="exchangeDT" />
					<div>
						<img src="../../../assets/mc.png" />
						<span>MPC</span>
					</div>
				</div>
				<!-- <div class="exchangeBalance colorA1BDE1">
					{{$t('RedeemableQuota')}} {{exchangeLimit}} DT
				</div> -->
				<button @click="exchange" v-ButClickLimit>{{$t('Commit')}}</button>
				
				<div class="exchangeWarning" @click="exchangeRules = true">{{ $t('notesOnExchange') }}</div>
			</div>
			<div class="exchangeRecord">
				<div class="exchangeRecordTitle" @click="App_Push('/changeRecord')">
					<span>{{$t('SwapRecords')}}</span>
					<div>
						<span>{{$t('All')}}</span>
						<!-- <img v-lazy="require('../../../assets/next.png')"> -->
						<self-icon type="next"></self-icon>
					</div>
				</div>
				<div class="exchangeRecordBox">					
					<div class="exchangeRecordList" v-for="(item,index) in list" :key="index">
						<div>
							<span>{{$t('SwapSuccess')}}</span>
							<span>{{item.time}}</span>
						</div>
						<div>
							<div>
								<span>-{{item.inAmount}} USDT</span>
								<span>+{{item.outAmount}} MPC</span>
							</div>
							<icon name='arrow'></icon>
						</div>
					</div>
					<!-- <not-record v-if="!list.length" :text="NoRecord"></not-record> -->
				</div>
			</div>
		</div>
		
		<authorization
		:show="authorizedAccess" 
		:click="authorizedAccessFun"
		:needAuthorization='value' 
		:tokenType="'USDT'"
		:authorizationEnds="authorizationEnds"
		@authorizationEnds="end"
		></authorization>
		
		<overlay :show="exchangeRules" class="exchangeRules" @click="exchangeRules = false">
			<div class="colorA1BDE1_1">
				<div class="title">{{ $t('notesOnExchange') }}</div>
				<div class="content">
					<p>{{ $t('notes1') }}</p>
					<p>{{ $t('notes2') }}</p>
					<p>{{ $t('notes3') }}</p>
				</div>
			</div>
		</overlay>
		
	</div>
</template>

<script>
	import { Overlay, Button , Icon , Toast } from 'vant'
	import Authorization from '@/components/Authorization.vue'
	import notRecord from '@/components/not-record'
	import selfIcon from '@/components/self-icon'
	import navBar from '@/components/nav-bar'
	
	export default {
		components: { Overlay, Button , Icon , Authorization , notRecord , selfIcon , navBar },
		data() {
			return {
				// show: false
				value: '',
				exchangeDT: '',
				info: '',
				prop: '',
				usdt: 0,
				total: '',
				current: '',
				RemainingProp: 0,
				authorizedAccess: false,
				USDTAuthorizedAccess: 0,
				authorizationEnds: false,
				list: [],
				Remaining: 0,
				roundID: '',
				NoRecord: this.$t('NoRecord'),
				exchangeLimit: 0,
				title: this.$t('swap'),
				exchangeRules: false,
			}
		},
		methods:{
			async init() {
				this.authorizedAccess = false;
				this.Log_Swaped();

				// 查询dt合约里面的dt
				// this.$tronWeb.call({
				// 	contractAddress: this.$contracts.referenceToken.address,
				// 	method: 'balanceOf(address)',
				// 	issuerAddress: this.$address,
				// 	parameter: [{ type: "address", value: this.$address }]
				// }).then(result => {
				// 	console.log(result.constant_result[0].decode(['uint256']).toString().fromSun())
				// })

				// usdt
				this.$tronWeb.call({
					contractAddress: this.$contracts.ERC20Token.address,
					method: 'balanceOf(address)',
					parameter: [{ type: "address", value: this.$address }]
				}).then(result => {
					this.usdt = result.constant_result[0].decode(['uint256']).toString().fromSun();
				})

				//usdt授权额度
				this.$tronWeb.call({
					contractAddress: this.$contracts.ERC20Token.address,
					method: 'allowance(address,address)',
					parameter: [
						{ type: "address", value: this.$address },
						{ type: "address", value: this.$contracts.Swap.address }
					]
				}).then(result => {
					this.USDTAuthorizedAccess = result.constant_result[0].decode(['uint256']).toString().fromSun();
				});

				this.$tronWeb.call({
					contractAddress: this.$contracts.Swap.address,
					method: 'swapInfo()',
				}).then(Info => {
					this.info = Info.constant_result[0].decode(['uint256','uint256','uint256','uint256'],['roundID','total','current','prop']);

					if(Number(this.info.roundID) < 10){
						this.roundID = "00"+this.info.roundID
					}else if(Number(this.info.roundID) < 100){
						this.roundID = "0"+this.info.roundID
					}else{
						this.roundID = this.info.roundID
					}

					this.prop = this.info.prop.mul('1'.toSun()).toString().fromSun()
					this.total = this.info.total.toString().fromSun()
					this.current = this.info.current.toString().fromSun()
					this.Remaining = this.info.total.sub(this.info.current).toString().fromSun()
					this.RemainingProp = this.Remaining.toBN().multipliedBy(100).div(this.total).toString().toFixed(2)
				})

				this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'selectOrders(address,uint256,uint256,uint256)',
					parameter: [
						{ type: "address", value: this.$address },
						{ type: "uint256", value: 65535 },
						{ type: "uint256", value: 0 },
						{ type: "uint256", value: 0 }
					]
				}).then( res => {
					const { total } = res.constant_result[0].decode(
						['uint256', 'uint256', 'uint256[]', 'uint256[]', 'uint8[]', 'uint96[]'],
						['total', 'len', 'orderIds', 'totalAmounts', 'states', 'times']
					);

					// 兑换上限
					if (total.toNumber()) {
						this.$tronWeb.call({
							contractAddress: this.$contracts.Swap.address,
							method: 'exchangeLimit(address)',
							parameter: [{ type: "address", value: this.$address }]
						}).then(result => {
							this.exchangeLimit = result.constant_result[0].decode(['uint256']).toString().fromSun()
							this.exchangeLimit = Number(this.exchangeLimit) * Number(this.prop)
						})
					} else {
						this.$contracts.Swap.nomalAddressQuota().call().then(result => {
							this.exchangeLimit = result.toNumber().fromSun()
							this.exchangeLimit = Number(this.exchangeLimit) * Number(this.prop)
						})
					}
				}).catch(err => {
					this.$contracts.Swap.nomalAddressQuota().call().then(result => {
						this.exchangeLimit = result.toNumber().fromSun()
						this.exchangeLimit = Number(this.exchangeLimit) * Number(this.prop)
					})
				});
			},
			input() {
				if(this.value == '' || this.value == 0){
					this.exchangeDT = ''
					return 
				}
				const value = this.value.toString();
				this.exchangeDT = value * this.prop;
			},
			async exchange() {
				var value = Number(this.value);
				if(value == '' || value == 0){
					return Toast.fail(this.$t('PleaseEnterTheTransferAmount'))
				}else if(value > this.usdt){
					return Toast.fail(this.$t('InsufficientBalance'))
				}else if(this.exchangeDT < 1){
					return Toast.fail(this.$t('LessThanTheMinimumLimit'))
				}else if(this.exchangeDT > this.exchangeLimit){
					return Toast.fail(this.$t('RedeemableLimitExceeded'))
				}else if(this.exchangeDT > this.Remaining){
					return Toast.fail(this.$t('ExcessRemaining'))
				} else if (value > Number(this.USDTAuthorizedAccess)) {
					return this.authorizedAccess = true;
				}
				Toast.loading({
					message: '正在交易中...',
					forbidClick: true,
					duration: 0
				});

				this.$contracts.Swap.doswaping(this.value.toSun())
				.send({ feeLimit: '50'.toSun() }).then(hex => {
					sessionStorage.setItem('transactionHex', hex);

					const interval = setInterval(() => {
						window.tronWeb.trx.getTransaction(hex).then(transaction => {
							if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
								sessionStorage.removeItem('transactionHex');
								clearInterval(interval);

								Toast.clear();
								this.value = '';
								this.exchangeDT = '';
								Toast.success(this.$t('SwapSuccess'));
								this.init();
							}
						})
					}, 2000);

				}).catch(err => {
					Toast.clear();
					console.log(err);
				});

				// this.$tronWeb.call({
				// 	contractAddress: this.$contracts.Swap.address,
				// 	method: 'doswaping(uint256)',
				// 	parameter: [{ type: "uint256", value: this.value.toSun() }]
				// }).then(async res => {
					
				// }).catch(err => {
				// 	Toast.clear();
				// 	console.log(err);
				// });
			},
			authorizedAccessFun(value) {
				Toast.loading({
					message: '正在交易中...',
					forbidClick: true,
					duration: 0
				});

				this.$contracts.ERC20Token.approve(this.$contracts.Swap.address, value.toSun())
				.send({ feeLimit: '50'.toSun() }).then(hex => {
					sessionStorage.setItem('transactionHex', hex);

					const interval = setInterval(() => {
						window.tronWeb.trx.getTransaction(hex).then(transaction => {
							if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
								sessionStorage.removeItem('transactionHex');
								clearInterval(interval);

								this.$tronWeb.call({
									contractAddress: this.$contracts.ERC20Token.address,
									method: 'allowance(address,address)',
									parameter: [
										{ type: "address", value: this.$address },
										{ type: "address", value: this.$contracts.Swap.address }
									]
								}).then(result => {
									this.USDTAuthorizedAccess = result.constant_result[0].decode(['uint256']).toString().fromSun();

									this.authorizedAccess = false;
									this.authorizationEnds = true;
									Toast.clear();
									this.exchange();
								});
							}
						})
					}, 2000);
				}).catch(err => {
					Toast.clear();
					console.log(err);
				});
			},
			end() {
				this.authorizedAccess = false;
			},
			Log_Swaped(){
				// this.$contracts.swap.events.Log_Swaped( {} , ( err , res ) => {
				// 	console.log(res)
				// })
				// this.$contracts.swap.Log_Swaped().watch({filters: {"owner": this.$address}},(err , result) => {
				// 	console.log(result)
				// })

				// this.$contracts.usdtToken.transfer('TM7TDAT7jfod772BsetornRtQH3Wz8ArUr','10000'.toSun()).send({
				// 	shouldPollResponse: true,
				// }).then(res => {
				// 	console.log(res)
				// }).catch(console.error);
				// console.log(this.$contracts.swap.Log_Swaped().watch)
				// this.$contracts.swap.Log_Swaped().watch((err,result)=>{
				// 	console.log(result)
				// })
				this.list = [];

				this.$tronWeb.event({
					contractAddress: this.$contracts.Swap.address,
					method: 'Log_Swaped'
				}).then(res => {
					for(let i = 0; i < res.length ; i++){
						if(window.tronWeb.address.fromHex(res[i].result.owner) != this.$address) continue
						this.list.unshift({
							inAmount: res[i].result.inAmount.fromSun(),
							outAmount: res[i].result.outAmount.fromSun(),
							time: this.$toTime(res[i].result.time,'Y-M-D H:i'),
						})
					}
				})
			}
		},
		created() {
			this.$tronWeb && this.init();
			// var fromBlock = (await this.$web3.eth.getBlock('latest').then()).number;
			// if(fromBlock - 7200 < 0){
			// 	fromBlock = 0
			// }else{
			// 	fromBlock -= 7200
			// }
			// var address = this.$address
			// this.$contract.getData('swapWs').events.Log_Swaped({
			// 	fromBlock: fromBlock,
			// 	filter: {owner: address}
			// }, (error, event)=>{ })
			// .on('data', async event => {	
			// 	this.list.unshift({
			// 		inAmount: this.$fromWei(event.returnValues.inAmount,'','Mwei'),
			// 		outAmount: this.$fromWei(event.returnValues.outAmount),
			// 		time: this.$toTime(event.returnValues.time,'Y-M-D H:i'),
			// 	})
			// })
			// .on('changed', function(event){
			// })
			// .on('error', console.error);
		}
	}
</script>

<style lang="scss" scoped>
	.exchangeBox{
		padding: 20px 30px 20px;
		height: calc(100vh - 120px - 60px);
		// display: flex;
		// flex-direction: column;
	}
	.exchangeInputBox{
		width: 100%;
		// height: 722px;
		padding: 20px 44px 22px;
		background-color: #fff;
		border: 1px solid #ffb600;
		// box-shadow: 0 6px 4px #000000;
		border-radius: 15px;
		color: #656363;
		
		.exchangeTitle{
			font-size: 36px;
			// color: #A1BDE1;
			margin-bottom: 10px;
			padding-bottom: 10px;
			// border-bottom: 2px solid #18222F;
		}
		
		.exchangeProp{
			font-size: 32px;
			display: flex;
			justify-content: space-between;
			// color: #A1BDE1;
		}
		
		.exchangeSchedule{
			width: 100%;
			height: 36px;
			border-radius: 10px;
			background-color: #E8E8E8;
			position: relative;
			margin: 10px 0 36px;
			
			&>div{
				width: 0%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				background: #18A664;
				border-radius: 10px;
				
				&>div{
					position: absolute;
					color: #fff;
					font-size: 24px;
					left: 10px;
					top: 50%;
					transform: translateY(-50%);
					text-align: left;
				}
			}
		}
		
		.exchangeInput{
			display: flex;
			width: 100%;
			height: 112px;
			font-size: 36px;
			background-color: #E8E8E8;
			// border: 2px solid #4d6784;
			border-radius: 10px;
			
			&>input{
				width: 381px;
				height: 112px;
				background-color: transparent;
				border: none;
				padding-left: 30px;
			}
			
			&>div{
				flex: 1;
				height: 108px;
				// color: #A1BDE1;
				border-left: 2px solid #dededf;
				display: flex;
				// justify-content: center;
				align-items: center;
				padding-left: 20px;
				
				&>img{
					width: 43px;
					margin-right: 34px;
				}
			}
		}
		
		.exchangeBalance{
			// color: #A1BDE1;
			font-size: 30px;
			text-align: left;
			margin: 14px 0;
		}
		
		&>button{
			width: 400px;
			height: 96px;
			border-radius: 50px;
			font-size: 40px;
			color: #fff;
			border: none;
			margin-top: 30px;
			box-shadow: 0 4px 12px rgba(#000, 0.16);
			// background: linear-gradient(90deg, #44F1A0 0%, #0C5734 100%);
			background-color: #21C17A;
		}
		
		.exchangeWarning{
			font-size: 26px;
			margin-top: 20px;
		}
	}
	
	.exchangeRecord{
		width: 100%;
		background-color: #fff;
		margin-top: 26px;
		display: flex;
		flex-direction: column;
		color: #656363;
		border-radius: 8px;
		// flex: 1;
		// height: 500px;
		position: relative;
		box-shadow: 0 4px 4px rgba(#000, 0.3);
		
		&>.exchangeRecordTitle{
			width: 100%;
			height: 100px;
			padding: 0 36px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			font-size: 32px;
			// border-bottom: #17212E 2px solid;
			
			&>div{
				line-height: 1;

				&>svg{
					font-size: 28px;
				}
			}
		}
		
		&>.exchangeRecordBox{
			width: 100%;
			flex: 1;
			height: calc(750px - 100px);
			padding: 0 36px;
			overflow-y: scroll;
			
			&>.exchangeRecordList{
				width: 100%;
				height: 98px;
				border-bottom: #E3E3E3 2px solid;
				font-size: 26px;
				display: flex;
				justify-content: space-between;
				align-items: center;
				
				&>div:nth-child(1){
					display: flex;
					flex-direction: column;
					text-align: left;
					
					&>span:nth-child(1){
						color: #21C17A;
					}
				}
				
				&>div:nth-child(2){
					display: flex;
					align-items: center;
					
					&>div{
						display: flex;
						flex-direction: column;
						text-align: right;
						margin-right: 32px;
					}
				}
			}
			
			&>.noRecord{
				display: flex;
				flex-direction: column;
				align-items: center;
				color: #3B4E66;
				font-size: 24px;
				margin-top: 50px;
				
				img{
					width: 67px;
					height: 85px;
					margin-bottom: 16px;
				}
			}
		}
	}
	
	.exchangeRules{
		padding: 116px 40px 62px;
		display: flex;
		align-items: center;
	
		&>div{
			width: 100%;
			// height: 100%;
			border-radius: 10px;
			background-color: #fff;
			color: #333;
	
			&>.title{
				width: 100%;
				height: 110px;
				font-size: 36px;
				line-height: 110px;
				border-bottom: 1px solid #516A89;
			}
	
			&>.content{
				width: 100%;
				// height: calc(100% - 110px);
				// flex: 1;
				padding: 30px 20px 30px 30px;
				font-size: 28px;
				overflow-y: scroll;
				text-align: left;
				line-height: 1.6;
	
				.marginBottom{
					margin-bottom: 20px;
				}
			}
		}
	}
</style>
