import Vue from 'vue'
import App from './App.vue'
import 'amfe-flexible/index.min.js'
import '@/components/iconfont.js'
import router from './router'
import { Lazyload } from 'vant'
import VueClipboard from 'vue-clipboard2'
import selfIcon from '@/components/self-icon.vue'
import i18n from '@/i18n/i18n.js'
//导入合约config文件
import '@/common/config.js'
Vue.config.productionTip = false
Vue.component('self-icon', selfIcon)
Vue.use(Lazyload)
Vue.use(VueClipboard)

class ShowWallet {
	constructor() {
		this.show = true
	}
	
	set(arg) {
		this.show = arg
	}
}

Vue.directive('ButClickLimit',{
	inserted: function(el){
		el.addEventListener('click',()=>{
			el.disabled = true;
			setTimeout(()=>{
				el.disabled = false;
			},2000)
		})
	}
})

Vue.prototype.App_Push = (path, key, value)=>{
	if (key && value) {
		const query = {};
		query[key] = value;

		return router.push({ path, query });
	}
	
	return router.push(path);
}

Vue.prototype.App_Pop = ()=>{
	return router.back()
}

Vue.prototype.PrefixZero = (value , length)=>{
	if(value.indexOf('0x') != -1){
		let result = (value.slice(2) + Array(length).join(0)).slice(0,length);
		return '0x'+result
	}
	return (value + Array(length).join(0)).slice(0,length);
}

Vue.prototype.$toTime = (times , type) => {
	function add(m){return m<10?'0'+m:m }
	var time = new Date(times * 1000);
	var y = time.getFullYear();
	var m = time.getMonth()+1;
	var d = time.getDate();
	var h = time.getHours();
	var mm = time.getMinutes();

	if (type === 'Y/M/D') {
		return y+'.'+add(m)+'.'+add(d);
	} else if (type === 'Y-M-D H:i') {
		return y+'-'+add(m)+'-'+add(d)+' '+add(h)+':'+add(mm);
	}else if(type === 'M/D H:i'){
		return add(m)+'/'+add(d)+' '+add(h)+':'+add(mm);
	}else if(type === 'Y/M/D H:i'){
		return y+'/'+add(m)+'/'+add(d)+' '+add(h)+':'+add(mm);
	}else if(type === 'YMD'){
		return y+add(m)+add(d);
	}else if(type === 'H:i'){
		return add(h)+':'+add(mm);
	}
}

new Vue({
	router,
	i18n,
	render: h => h(App)
}).$mount('#app')