			<div class="upLvTop">
				<img v-if="origin == 0" src="../../../assets/upLv00.png" />
				<img v-else-if="origin == 1" src="../../../assets/upLv01.png" />
				<img v-else-if="origin == 2" src="../../../assets/upLv02.png" />
				<img v-else-if="origin == 3" src="../../../assets/upLv03.png" />
				<img v-else-if="origin == 4" src="../../../assets/upLv04.png" />
				<img v-else-if="origin == 5" src="../../../assets/upLv05.png" />
			</div>
			<p class="colorA1BDE1">{{$t('currentLevel')}}</p>
			<span>{{Number(origin) ? 'M'+origin : 'VIP'}}</span>
			<div class="upLvBannerText colorA1BDE1">{{$t('EnjoyShortcutsWithoutWaiting')}}</div>
			<Button @click="App_Push('/grade')" v-ButClickLimit>{{$t('UpgradeNow')}}</Button>
			<Button @click="App_Push('/supernode')" v-ButClickLimit>{{ $t('supernode') }}</Button>
			<div class="upLvDetailsText" @click="upLvDetails = true">
				<span>{{$t('LearnMore')}}</span>
			</div>
			<!-- <div class="upLvIdentityIcon colorA1BDE1" v-if="origin != 0">
				<span>{{$t('levelIcon')}}</span>
				<div>
					<div>						
						<div>
							<img src="../../../assets/upLv00.png" />
							<span>VIP</span>
						</div>
						<div>
							<img src="../../../assets/upLv01.png" />
							<span>D1</span>
						</div>
						<div>
							<img src="../../../assets/upLv02.png" />
							<span>D2</span>
						</div>
					</div>
					<div>
						<div>
							<img src="../../../assets/upLv03.png" />
							<span>D3</span>
						</div>
						<div>
