		</div>
		
		
		<overlay :show="upLvDetails" class="upLvDetails">
			<div class="upLvText colorA1BDE1">
				<pre id="pre"><Icon name="cross" /></pre>
			</div>
		</overlay>
	</div>
</template>

<script>
	import Authorization from '@/components/Authorization.vue'
	import navBar from '@/components/nav-bar'
	import { Button, Toast, Overlay, Icon } from 'vant';

	export default{
		components:{ navBar , Button , Overlay , Authorization, Icon },
		data(){
			return {
				title: this.$t('levelUpgrade'),
				origin: '',
				DT: 0,
				upLvDetails: false,
				dlv1Prices: 0,
				authorizedAccess: false,
				authorizationEnds: false,
				usdt: 0,
			}
		},
		methods:{
			async init() {
				//查询等级
				this.$tronWeb.call({
					contractAddress: this.$contracts.Relations.address,
					method: 'levelOf(address)',
					parameter: [{ type: "address", value: this.$address }]
				}).then(result => {
					const origin = result.constant_result[0].decode(['uint256']);
					// 原等级
					this.origin = origin.toString();
				})
			}
		},
		created() {
			this.$tronWeb && this.init();
			this.$nextTick(() => {
				const pre = document.querySelector('#pre');
				pre.innerHTML += this.$t('upLvRule');

				const i = document.querySelector('#pre>i');
				i.addEventListener('click', () => {
					this.upLvDetails = false;
				})
			})
		}
	}
</script>

<style lang="scss">
	.upLvText>pre {
		line-height: 1px;
		&>.text {
