			line-height: 44px;
			margin-bottom: 14px;
			&::before {
				content: '';
				display: inline-block;
				width: 12px;
				height: 12px;
				border: 2px solid #FFB600;
				border-radius: 50%;
				margin: 12px 16px 0 0;
			}
		}
		&>.title {
			display: inline-block;
			color: #18A664;
			font-size: 36px;
			line-height: 50px;
			margin-bottom: 20px;
		}
	}
</style>

