		padding: 64px 32px 10px 32px;
		background-color: #fff;
		border: 2px solid #FFB600;
		color: #656363;
		overflow: auto;
		&>pre {
			width: 100%;
			white-space: pre-wrap;
			position: relative;
			&>i {
				position: absolute;
				right: 32px;
				font-size: 38px;
			}
		}
