<template>
	<div class="compenstate box">
		<!-- <nav-bar :title="title" rightIcon="record" :rightClick="pushCompensateRecord" ></nav-bar> -->
		<nav-bar :title="title"></nav-bar>
		<div class="navBox compenstateBox">
			<div class="compenstateCircle">
				<div>{{SettlementCompensate ? SettlementCompensate : currentAmout}}</div>
				<span>{{$t('Available02')}}/MPC</span>
			</div>
			<div class="compenstateInfo colorA1BDE1">
				<div>
					<img src="../../../assets/getReward.png" />
					<span>{{$t('Compensation')}}</span>
					<span>{{total}} MPC</span>
				</div>
				<p></p>
				<div>
					<img src="../../../assets/wait.png" />
					<span>{{$t('Locked')}}</span>
					<span>{{amout}} MPC</span>
				</div>
			</div>
			<Button @click="send" v-ButClickLimit>{{SettlementCompensate ? $t('Release') : $t('receive')}}</Button>
		</div>
	</div>
</template>

<script>
	import { Button , Toast } from 'vant'
	import navBar from '@/components/nav-bar'
	
	export default{
		components:{ navBar , Button },
		data(){
			return {
				title: this.$t('ReceiveDT'),
				SettlementCompensate: 0,
				total: 0,
				amout: 0,
				currentAmout: 0,
			}
		},
		methods:{
			async init(){
				this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'settlementCompensate()'
				}).then(async result => {
					this.SettlementCompensate = Number(result.constant_result[0].decode(['uint256']).toString().fromSun());
					if(!this.SettlementCompensate){
						const { total, currentWithdraw } = (await this.$tronWeb.call({
							contractAddress: this.$contracts.Compensate.address,
							method: 'compensateMapping(address)',
							parameter: [{ type: "address", value: this.$address }]
						}).then()).constant_result[0].decode(['uint256','uint256','uint256'],['total','currentWithdraw','latestWithdrawTime']);

						this.total = total.toNumber().fromSun();
						this.amout = total.sub(currentWithdraw.toString()).toString().fromSun();
						this.currentAmout = (await this.$tronWeb.call({
							contractAddress: this.$contracts.Compensate.address,
							method: 'withdrawCompensate()'
						}).then()).constant_result[0].decode(['uint256']).toString().fromSun();
					}
				})
			},
			pushCompensateRecord(){
				this.App_Push('/compensateRecord')
			},
			send(){
				Toast.loading({
					message: '正在交易中...',
					forbidClick: true,
					duration: 0
				});

				if (this.SettlementCompensate > 0) {
					this.$contracts.OrderManager.settlementCompensate()
					.send({ feeLimit: '10'.toSun() }).then(hex => {
						sessionStorage.setItem('transactionHex', hex);

						const interval = setInterval(() => {
							window.tronWeb.trx.getTransaction(hex).then(transaction => {
								if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
									sessionStorage.removeItem('transactionHex');
									clearInterval(interval);

									Toast.clear();
									this.init();
								}
							})
						}, 2000);

					}).catch(err => {
						Toast.clear();
						console.log(err);
					});
				} else {
					if (this.currentAmout <= 0) return Toast.fail(this.$t('NoDTAvailable'));

					this.$contracts.Compensate.withdrawCompensate()
					.send({ feeLimit: '10'.toSun() }).then(hex => {
						sessionStorage.setItem('transactionHex', hex);

						const interval = setInterval(() => {
							window.tronWeb.trx.getTransaction(hex).then(transaction => {
								if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
									sessionStorage.removeItem('transactionHex');
									clearInterval(interval);

									Toast.clear();
									this.init();
								}
							})
						}, 2000);

					}).catch(err => {
						Toast.clear();
						console.log(err);
					});
				}
			},
		},
		mounted() {
			this.$tronWeb && this.init();
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>
<style>
	.compenstate svg{
		font-size: 45px!important;
		/* color: #A1BDE1; */
	}
</style>
<style lang="scss" scoped>
	.compenstateBox{
		padding: 50px 24px 0;
		
		.compenstateCircle{
			display: flex;
			flex-direction: column;
			align-items: center;
			font-size: 28px;
			color: #18A664;
			
			&>div{				
				width: 240px;
				height: 240px;
				border-radius: 50%;
				background: linear-gradient(0deg,#0C5332,#36A270,#64D19E,#8AF8C5);
				font-size: 60px;
				margin-bottom: 28px;
				line-height: 240px;
				color: #FAFAFA;
			}
		}
		
		.compenstateInfo{
			width: 100%;
			height: 272px;
			border-radius: 20px;
			border: 2px solid #FFB600;
			background-color: #fff;
			display: flex;
			justify-content: space-between;
			margin: 70px 0 106px;
			color: #656363;
		
			// &>p{
			// 	border-right: 2px solid #18222F;
			// }
			
			&>div{
				flex: 1;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				
				&>img{
					display: block;
					// width: auto;
					height: 60px;
				}
				
				&>span:nth-child(2){
					font-size: 32px;
					margin: 20px 0 15px;
				}
				
				&>span:nth-child(3){
					font-size: 40px;
				}
			}
		}
		
		&>button{
			width: 400px;
			height: 96px;
			background: linear-gradient(270deg,#21C1A8 0%,#21C17A 100%);
			border: none;
			border-radius: 50px;
			font-size: 40px;
			color: #fff;
		}
	}
</style>
