<template>
	<div class="box friendsListBox">
		<nav-bar :title="title"></nav-bar>
		<div class="header">
			<div
			  v-for="(item, index) in history"
			  :key="index"
			  :class="{active: active == item.name}"
			  @click="viewHistory(index,item.name)"
			>{{ item.name }}</div>
		</div>
		<div class="content">
			<div
			  v-for="(item, index) in list"
			  :key="index"
			  @click="addHistory(item.name, item.address)"
			>
				<div>
					<div v-if="item.grade > 0">
						<span>{{ item.name }}</span>
						<img v-lazy="require(`@/assets/lv${item.grade}.png`)">
						<span>(V{{ item.grade }})</span>
					</div>
					<div v-else>
						<span>{{ item.name }}</span>
						<img v-lazy="require('@/assets/upLv00.png')">
					</div>
					<span>{{ item.code }}</span>
				</div>
				<div>
					<span>{{ item.amount }} <span>USDT</span></span>
					<img v-lazy="require('@/assets/next.png')">
				</div>
			</div>
			<not-record v-if="!list.length" :text="NoFriendsRecord"></not-record>
		</div>
	</div>
</template>

<script>
	import navBar from '@/components/nav-bar'
	import notRecord from '@/components/not-record'
	
	export default {
		components: { navBar, notRecord },
		data() {
			return {
				title: this.$t('FriendsList'),
				list: [
					// { name: '001', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '002', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '003', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '004', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '005', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '006', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '007', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '008', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '009', code: '123456', grade: 1, amount: '0.12', address: '123' },
				],
				history: [],
				NoFriendsRecord: this.$t('NoFriendsRecord'),
				active: '',
			}
		},
		created() {
			this.$tronWeb && this.init()
		},
		methods: {
			init() {
				this.getData()
			},
			getData() {
				this.$tronWeb.call({
					contractAddress: this.$contracts.Relations.address,
					method: 'addressToNickName(address)',
					issuerAddress: this.$address,
					parameter: [{ type: "address", value: this.$address }]
				}).then( result => {
					const res = result.constant_result[0].decode(['bytes16']);
					let name = res.hexToString(),
						regName = name.replace(/[^\u4e00-\u9fa5a-zA-Z\d,\.，。]+/,'');

					let history = [{
						name: regName,
						address: this.$address
					}]

					this.active = regName
					this.history = history
				})

				this.getList(this.$address)
			},
			getList(address) {
				this.$tronWeb.call({
					contractAddress: this.$contracts.Relations.address,
					method: 'recommendList(address)',
					parameter: [{ type: "address", value: address }]
				}).then(result => {
					let res = result.constant_result[0].decode(['address[]']),
						list = [];
					
					res.forEach(item => {
						Promise.all([
							this.$tronWeb.call({
								contractAddress: this.$contracts.Relations.address,
								method: 'addressToNickName(address)',
								parameter: [{ type: "address", value: item }]
							}),
							this.$tronWeb.call({
								contractAddress: this.$contracts.Relations.address,
								method: 'addressToShortCode(address)',
								parameter: [{ type: "address", value: item }]
							}),
							this.$tronWeb.call({
								contractAddress: this.$contracts.OrderManager.address,
								method: 'isValid(address)',
								parameter: [{ type: "address", value: item }]
							}),
							this.$tronWeb.call({
								contractAddress: this.$contracts.Relations.address,
								method: 'levelOf(address)',
								parameter: [{ type: "address", value: item }]
							}),
							this.$tronWeb.call({
								contractAddress: this.$contracts.OrderManager.address,
								method: 'userInfoMapping(address)',
								parameter: [{ type: "address", value: item }]
							})
						]).then(([ name, code, IsValid, Grade, Info ]) => {
							let nameDecode = name.constant_result[0].decode(['bytes16']),
								codeDecode = code.constant_result[0].decode(['bytes6']),
								isValid = IsValid.constant_result[0].decode(['bool']),
								grade = Grade.constant_result[0].decode(['uint256']),
								info = Info.constant_result[0].decode(['uint256','uint256','uint256','uint256'],['totalIn','totalOut','awardQuota','awardWithdrawableTotal']);
							
							list.push({
								name:  nameDecode.hexToString().replace(/[^\u4e00-\u9fa5a-zA-Z\d,\.，。]+/,''),
								code: codeDecode.hexToString(),
								grade: grade.toNumber(),
								amount: info.totalIn.toString().fromSun().toFixed(2),
								address: item
							})
						})
					})

					this.list = list
				})
			},
			viewHistory(index , name) {
				this.getList(this.history[index].address)
				this.history = this.history.slice(0, index + 1)
				this.active = name
			},
			addHistory(name, address) {
				this.getList(address)
				this.history.push({ name, address })
				setTimeout(() => {
					document.querySelector('.header>div:last-child').scrollIntoView({
						behavior: 'smooth'
					})
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.box{
		// padding-top: 40px;
	}
	.content {
		width: 100vw;
		height: calc(100vh - 200px);
		overflow-y: auto;
		position: relative;
		padding: 20px 0 0 30px;
		background-color: #fff;
		&>div {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding-right: 40px;
			margin-bottom: 20px;
			padding-bottom: 20px;
			border-bottom: 2px solid #AFAFAF;
			&>div {
				display: flex;
				&:first-child {
					color: #AFAFAF;
					font-size: 28px;
					flex-direction: column;
					align-items: flex-start;
					&>div {
						display: flex;
						align-items: center;
						margin-bottom: 20px;
						&>span {
							&:first-child {
								color: #656363;
								font-size: 32px;
							}
							&:last-child {
								color: #AFAFAF;
							}
						}
						&>img {
							width: 38px;
							height: 38px;
							margin: 0 19px;
						}
					}
				}
				&:last-child {
					align-items: center;
					&>span {
						color: #AFAFAF;
						font-size: 32px;
						&>span {
							font-size: 20px;
							margin-right: 18px;
						}
					}
					&>img {
						width: 15px;
						height: 30px;
					}
				}
			}
		}
		&>.not_record {
			margin: 0;
			padding: 0;
			border: none;
		}
	}
	.header {
		width: 100vw;
		height: 60px;
		font-size: 0;
		text-align: left;
		line-height: 60px;
		white-space: nowrap;
		margin-bottom: 20px;
		overflow: auto hidden;
		&::-webkit-scrollbar {
			display: none;
		}
		&>div {
			display: inline-block;
			width: 170px;
			height: 60px;
			color: #AFAFAF;
			font-size: 28px;
			margin-right: 5px;
			text-align: center;
			position: relative;
			background-color: #fff;
			&::before, &::after {
				content: '';
				width: 42px;
				height: 42px;
				position: absolute;
				top: 8px;
				transform: rotate(45deg);
			}
			&::before {
				left: -21px;
				background-color: #FCF9EC;
			}
			&::after {
				z-index: 1;
				right: -21px;
				background-color: #FCF9EC;
			}
			
			&:first-child::before {
				display: none;
			}
		}
		&>.active {
			color: #656363;
		}
	}
</style>
