<template>
	<div class="box">
		<nav-bar :title="$t('mandate')"></nav-bar>
		<div class="navBox authorizationManagementBox">
			<div class="usdtLogo">
				<img src="../../../assets/Ttoken.png" />
			</div>
			<div class="CurrentAuthorization colorA1BDE1">
				<span>{{$t('AuthorizationManagementText10')}}</span>
				<div>{{USDTAuthorizedAccess}} USDT</div>
				<p>{{$t('AuthorizationManagementText01')}}</p>
			</div>
			<div class="authorizationManagementText">
				<p v-if="USDTAuthorizedAccess > 0">{{$t('AuthorizationManagementText02')}}</p>
				<p v-if="USDTAuthorizedAccess > 0">{{$t('AuthorizationManagementText03')}}</p>
				<p v-if="USDTAuthorizedAccess === 0">{{$t('AuthorizationManagementText04')}}</p>
				<div class="cause" v-if="USDTAuthorizedAccess > 0" @click="pushHref">
					<span>{{$t('AuthorizationManagementText05')}}</span>
					<p>https://github.com/ethereum/EIPs/issues/20#issuecomment-263524729</p>
				</div>
				<div v-if="USDTAuthorizedAccess === 0">{{$t('AuthorizationManagementText06')}}</div>
			</div>
			<div class="checkbox" @click="checked = !checked" v-if="USDTAuthorizedAccess === 0">
				<icon name="success" :class="checked ? 'active' : ''"></icon>
				<span>{{$t('AuthorizationManagementText07')}}</span>
			</div>
			<button :disabled="USDTAuthorizedAccess > 0 ? false : !checked" @click="send" v-ButClickLimit>{{USDTAuthorizedAccess === 0 ? $t('AuthorizationManagementText08') :$t('AuthorizationManagementText09')}}</button>
		</div>
	</div>
</template>

<script>
	import navBar from '@/components/nav-bar'
	import { Icon, Toast } from 'vant'
	export default{
		components:{ navBar , Icon },
		data(){
			return {
				checked: false,
				USDTAuthorizedAccess: '0',
			}
		},
		mounted() {
			this.$tronWeb && this.init()
		},
		methods:{
			init(){
				// usdt授权额度
				this.$tronWeb.call({
					contractAddress: this.$contracts.ERC20Token.address,
					method: 'allowance(address,address)',
					issuerAddress: this.$address,
					parameter: [{ type: "address", value: this.$address },{ type: "address", value: this.$contracts.OrderManager.address }]
				}).then( result => {
					const res = result.constant_result[0].decode(['uint256'])
					this.USDTAuthorizedAccess = Number(res.toString().fromSun())
				})
			},
			send(){
				if(this.USDTAuthorizedAccess === 0){
					var value = '10000'
				}else if(this.USDTAuthorizedAccess > 0){
					var value = '0'
				}
				Toast.loading({
					message: '正在交易中...',
					forbidClick: true,
					duration: 0
				});

				this.$contracts.ERC20Token.approve(this.$contracts.OrderManager.address,value.toSun()).send({
					from : this.$address,
					feeLimit: '50'.toSun()
				}).then(res => {
					Toast.clear();
					this.init();
					this.App_Pop();
				}).catch(err => {
					Toast.clear();
					console.log(err);
				})
			},
			pushHref(){
				window.location.href = "https://github.com/ethereum/EIPs/issues/20#issuecomment-263524729"
			}
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.authorizationManagementBox{
		padding: 0 40px;
		color: #656363;
		
		.usdtLogo{
			width: 128px;
			height: 128px;
			background-color: #1ba27a;
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 auto 40px;

			&>img{
				width: 60%;
			}
		}
		
		.CurrentAuthorization{
			font-size: 32px;
			
			&>div{
				font-size: 40px;
				margin: 12px 0 0;
				font-weight: bold;
			}
			
			&>p{
				font-size: 36px;
				margin: 40px 0;
			}
		}
		
		.authorizationManagementText{
			padding: 36px;
			
			font-size: 28px;
			text-align: left;
			border-radius:0px 40px 0px 40px;
			background-color: #fff;
			border: 2px solid #FFB600;
			
			&>p{
				text-indent: 1em;
			}
			
			&>.cause{
				margin-top: 20px;
				word-break: break-word;
				position: relative;
				line-height: 1.3;
				color: #639EEA;
				
				&>p{
					text-indent: 3em;
				}
				
				&>span{
					color: #4D6481;
					position: absolute;
					left: 0;
				}
			}
		}
		
		.checkbox {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			margin-top: 40px;
			font-size: 30px;
			color: #18A664;
		
			&>i {
				width: 40px;
				height: 40px;
				line-height: 40px;
				color: transparent;
				margin-right: 15px;
				border-radius: 50%;
				border: 2px solid #18A664;
				&.active {
					color: #18A664;
					background-color: transparent;
				}
			}
			&>span {
				font-size: 28px;
				text-align: left;
			}
		}
		
		button{
			width: 400px;
			height: 96px;
			border-radius: 50px;
			background-color: #21C17A;
			font-size: 40px;
			color: #fff;
			border: none;
			margin-top: 100px;
		}
		
		button:disabled{
			opacity: .5;
		}
	}
</style>
