<template>
	<div>
		<div class="box van-settingBox">
			<div class="card" @click="App_Push(bind ? '/upLv' : '/register')">
				<div class="card_lv">
					<img src="@/assets/upLvTopBg.png" />

					<img v-if="origin == '0'" v-lazy="require('@/assets/upLv00.png')">
					<img v-if="origin == '1'" v-lazy="require('@/assets/upLv01.png')">
					<img v-if="origin == '2'" v-lazy="require('@/assets/upLv02.png')">
					<img v-if="origin == '3'" v-lazy="require('@/assets/upLv03.png')">
					<img v-if="origin == '4'" v-lazy="require('@/assets/upLv04.png')">
					<img v-if="origin == '5'" v-lazy="require('@/assets/upLv05.png')">
				</div>
				<div 
					class="card_code copy"
					@click.stop
					v-clipboard:copy="code"
					v-clipboard:success="onCopySuccess"
					v-clipboard:error="onCopyError"
				>
					<span>{{ code }}</span>
					<img v-lazy="require('@/assets/copy.png')">
				</div>
				<div
					class="copy card_address"
					@click.stop
					v-clipboard:copy="address"
					v-clipboard:success="onCopySuccess"
					v-clipboard:error="onCopyError"
				>
					<span>{{ address ? `${address.slice(0, 10)}...${address.slice(-10)}` : '' }}</span>
				</div>
			</div>

			<div class="content">
				<div class="horizontal_list">
					<div @click="bind ? App_Push('/card') : App_Push('/register')">
						<img src="@/assets/invite.png"/>
						<span>{{$t('invite')}}</span>
					</div>
					<div @click="help">
						<img src="@/assets/communityHelp.png"/>
						<span>{{$t('help')}}</span>
					</div>
					<div @click="InMessage">
						<img src="@/assets/message.png"/>
						<span>{{$t('News')}}</span>
					</div>
				</div>
				<div class="vertical_list">
					<div @click="bind ? App_Push('/friendsList') : App_Push('/register')">
						<div>
							<img src="@/assets/inviteList.png"/>
							<span>{{$t('InvitationList')}}</span>
						</div>
						<div>
							<self-icon type="next"></self-icon>
						</div>
					</div>
					<div>
						<div>
							<img src="@/assets/MyInviter.png"/>
							<span>{{$t('MyInviter')}}</span>
						</div>
						<div class="settingContentRight"  @click="seeActive = !seeActive">
							<span>{{seeActive ? '******' : GetIntroducerCode}}</span>
							<self-icon type="see" class="see" v-show="!seeActive"></self-icon>
							<self-icon type="seeActive" class="seeActive" v-show="seeActive"></self-icon>
						</div>
					</div>
					<div @click="bind ? App_Push('/compensate') : App_Push('/register')">
						<div>
							<img src="@/assets/ReceiveDT.png"/>
							<span>{{$t('ReceiveDT')}}</span>
						</div>
						<div>
							<self-icon type="next"></self-icon>
						</div>
					</div>
					<div @click="App_Push('/language')">
						<div>
							<img src="@/assets/language.png"/>
							<span>{{$t('language')}}</span>
						</div>
						<div>
							<self-icon type="next"></self-icon>
						</div>
					</div>
					<div @click="bind ? App_Push('/nodalDividend') : App_Push('/register')">
						<div>
							<img src="@/assets/nodalDividend.png"/>
							<span>{{$t('USDTAuthorizationManagement')}}</span>
						</div>
						<div>
							<self-icon type="next"></self-icon>
						</div>
					</div>
				</div>
			</div>
		</div>
		<overlay :show="show" class="set_name" @click="show = false">
			<div @click.stop>
				<span>{{$t('SetUp')}}</span>
				<input type="text" v-model="nickname" :placeholder="$t('EnterNewNickname')">
				<div>
					<Button @click="show = false">{{$t('cancel')}}</Button>
					<i></i>
					<Button @click="setName" v-ButClickLimit>{{$t('Commit')}}</Button>
				</div>
			</div>
		</overlay>
		
		<overlay :show="upLv" class="upLv" @click="upLv = false">
			<div @click.stop>
				<img v-if="Number(current) == 1" src="../../../assets/upLv1.png" />
				<img v-if="Number(current) == 2" src="../../../assets/upLv2.png" />
				<img v-if="Number(current) == 3" src="../../../assets/upLv3.png" />
				<img v-if="Number(current) == 4" src="../../../assets/upLv4.png" />
				<img v-if="Number(current) == 5" src="../../../assets/upLv5.png" />
				<div>{{$t('Promoted')}}D{{current}}{{$t('level')}}</div>
				<div @click="upLvFun" v-ButClickLimit>{{$t('receive')}}</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import { Overlay, Button, Toast } from 'vant'
	import selfIcon from '@/components/self-icon.vue'
	import { setInterval } from 'timers'
	
	export default {
		components: { Overlay, Button , selfIcon },
		data() {
			return {
				name: 'DeFi Era',
				code: ' ',
				nickname: '',
				show: false,
				upLv: false,
				message: localStorage.getItem('InMessage') ? JSON.parse(localStorage.getItem('InMessage')) : true,
				bind: false,
				origin: '',
				current: '',
				GetIntroducerCode: '',
				seeActive: false,
				address: '',
			}
		},
		mounted() {
			this.$tronWeb && this.init();
		},
		methods: {
			init() {
				this.upLv = false;
				this.show = false;
				this.nickname = '';
				this.address = this.$address
				
				this.getData()
			},
			getData() {
				if(this.$route.query.invitationCode && this.$route.query.invitationCode.length == 6){localStorage.setItem('invitationCode',this.$route.query.invitationCode)}

				this.$tronWeb.call({
					contractAddress: this.$contracts.Relations.address,
					method: 'addressToShortCode(address)',
					parameter: [{ type: "address", value: this.$address }]
				}).then(result=>{
					const res = result.constant_result[0].decode(['bytes6'])
					
					let code = res.hexToString();
					
					this.bind = !(Number(res) === 0)
					this.code = !parseInt(res,16) ? '******' : code
					
					if(this.bind){
						this.$tronWeb.call({
							contractAddress: this.$contracts.Relations.address,
							method: 'getIntroducer(address)',
							parameter: [{ type: "address", value: this.$address }]
						}).then(async result=>{
							const res = result.constant_result[0].decode(['address'])

							let GetIntroducerCode = (await this.$tronWeb.call({
								contractAddress: this.$contracts.Relations.address,
								method: 'addressToShortCode(address)',
								issuerAddress: this.$address,
								parameter: [{ type: "address", value: res }]
							}).then()).constant_result[0].decode(['bytes6']);

							if(parseInt(GetIntroducerCode,16)){
								this.GetIntroducerCode = GetIntroducerCode.hexToString()
							}else{
								this.GetIntroducerCode = ''
							}
						})
						

						this.$tronWeb.call({
							contractAddress: this.$contracts.Relations.address,
							method: 'addressToNickName(address)',
							issuerAddress: this.$address,
							parameter: [{ type: "address", value: this.$address }]
						}).then(async result=>{
							const res = result.constant_result[0].decode(['bytes16'])
							let name = res.hexToString(),
								regName = name.replace(/[^\u4e00-\u9fa5a-zA-Z\d,\.，。]+/,'')
							
							this.name = regName === '' ? 'DeFi Era' : regName
						})
					}
				})

				this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
					method: 'upgradeDLevel()'
				}).then(async result=>{
					const { origin , current } = result.constant_result[0].decode(['uint256','uint256'],['origin','current'])

					// 原等级
					this.origin = origin.toString();
					// 可升等级
					this.current = current.toString();

					if(this.current != this.origin){
						this.upLv = true;
					}
					
				})
			},
			upLvFun(){
				Toast.loading({
					message: '正在交易中...',
					forbidClick: true,
					duration: 0
				});

				this.$contracts.Manager.upgradeDLevel()
				.send({ feeLimit: '50'.toSun() }).then(hex => {
					sessionStorage.setItem('transactionHex', hex);

	                const interval = setInterval(() => {
	                    window.tronWeb.trx.getTransaction(hex).then(transaction => {
	                        if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
	                            sessionStorage.removeItem('transactionHex');
	                            clearInterval(interval);
	
								Toast.clear();
								this.init();
	                        }
	                    })
	                }, 2000);

				}).catch(err => {
					Toast.clear();
					console.log(err);
				});
			},
			async setName() {
				let reg = /^[\u4e00-\u9fa5_a-zA-Z0-9]+$/
				
				if (!reg.test(this.nickname) || this.nickname.length > 8) {
					return Toast.fail(this.$t('warningText13'))
				}
				Toast.loading({
					message: '正在交易中...',
					forbidClick: true,
					duration: 0
				});

				this.$contracts.Relations.UpdateNickName(this.PrefixZero(this.nickname.stringToHex(),32))
				.send({ feeLimit: '10'.toSun() }).then(hex => {
					sessionStorage.setItem('transactionHex', hex);

	                const interval = setInterval(() => {
	                    window.tronWeb.trx.getTransaction(hex).then(transaction => {
	                        if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
	                            sessionStorage.removeItem('transactionHex');
	                            clearInterval(interval);
	
								Toast.clear();
								this.init();
	                        }
	                    })
	                }, 2000);
				}).catch(console.error);
			},
			onCopySuccess() {
				Toast.success(this.$t('CopySuccessful'))
			},
			onCopyError() {
				Toast.fail(this.$t('CopyFailed'))
			},
			help(){
				if(this.bind){
					// location.href = 'https://defiera.zendesk.com';
					Toast(this.$t('NotYetOpen'));
				}else{
					this.App_Push('/register')
				}
			},
			InMessage(){
				if(this.bind){
					localStorage.setItem('InMessage',false)
					this.App_Push('/message')
				}else{
					this.App_Push('/register')
				}
			},
			modifyName(){
				if(this.bind){
					this.show = true
				}else{
					this.show = false
				}
			},
		},
	}
</script>

<style lang="scss" scoped>
	.van-settingBox{
		width: 100vw;
		height: calc(100vh - 25px);
		overflow-y: auto;
		background: linear-gradient(132deg, #44F1A0 0%, #0C5734 100%);
		display: flex;
		flex-direction: column;
	}
	.upLv{
		// padding: ;
		&>div{
			position: relative;
			&>img{
				width: 100%;
			}
			&>div:nth-child(2){
				position: absolute;
				top: 548px;
				color: #FFEA50;
				font-size: 50px;
				font-weight: bold;
				left: 50%;
				transform: translateX(-50%);
			}			
			&>div:nth-child(3){
				position: absolute;
				top: 672px;
				width: 261px;
				height: 83px;
				color: #FAFAFA;
				border: 2px solid #FFEA50;
				font-size: 40px;
				left: 50%;
				transform: translateX(-50%);
				text-align: center;
				line-height: 83px;
				border-radius: 58px;
			}
		}
	}
	.set_name {
		padding: 308px 74px 0;
		&>div {
			width: 100%;
			display: flex;
			align-items: center;
			flex-direction: column;
			padding-top: 28px;
			// background-color: #2A405C;
			// color: #C5D1E1;
			font-size: 40px;
			border-radius: 10px;
			&>input {
				border: none;
				width: calc(100% - 68px);
				padding: 30px;
				margin-top: 46px;
				margin-bottom: 47px;
				border-radius: 15px;
				// background-color: #101720;
			}
			&>div {
				width: 100%;
				display: flex;
				align-items: center;
				border-top: 2px solid #273446;
				&>button {
					border: none;
					width: calc(50% - 1px);
					height: 103px;
					line-height: 103px;
					// color: #C5D1E1;
					font-size: 40px;
					background-color: transparent;
				}
				&>i {
					width: 2px;
					height: 103px;
					background-color: #273446;
				}
			}
		}
	}
	.box {
		// padding: 40px 24px 0;
		&>.content {
			width: 100%;
			padding: 52px 40px;
			flex: 1;
			background-color: #fff;
			border-radius: 40px 40px 0 0;
			font-size: #333;

			&>.horizontal_list{
				width: 100%;
				display: flex;
				justify-content: space-between;
				font-size: 30px;
				padding: 0 40px;
				margin-bottom: 80px;

				&>div{
					display: flex;
					flex-direction: column;

					&>img{
						width: 104px;
						margin-bottom: 14px;
					}
				}
			}

			&>.vertical_list{
				width: 100%;
				display: flex;
				flex-direction: column;
				font-size: 30px;

				&>div{
					display: flex;
					align-items: center;
					justify-content: space-between;
					padding-bottom: 20px;
					border-bottom: 1px dotted #DCDDDF;
					margin-bottom: 20px;

					&>div:first-child{
						display: flex;
						align-items: center;
						font-size: 38px;

						&>img{
							width: 80px;
						}
					}

					&>div:last-child{
						svg{
							font-size: 30px;
							color: #DCDDDF;
						}
					}

					&>.settingContentRight {
						color: #666;
						font-size: 36px;

						&>.see,&>.seeActive{
							width: 47px;
							height: 31px;
							margin-left: 20px;
						}
					}
				}
			}
		}
		&>.card {
			padding: 70px 0 78px;
			color: #fff;

			&>.card_lv{
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;

				&>img:first-child{
					width: 212px;
				}

				&>img:last-child{
					position: absolute;
					width: 104px;
				}
			}

			&>.card_code{
				font-size: 38px;
				margin: 5px 0 10px;

				&>img{
					width: 27px;
					margin-left: 18px;
				}
			}

			&>.card_address{
				font-size: 30px;
			}
		}
	}
</style>
