<template>
	<div class="box">
		<nav-bar :title='title'></nav-bar>
		<div class="navBox orderDetailsBoxBox">
			<div class="orderDetailsBox">
				<div class="orderDetailsBoxTop">
					<div class="orderDetailsBoxTopCircle" v-if="list.IsBreaker === false">
						<icon name="success" class="active"></icon>
						<div :style="{ width: `${isComplete ? 100 : 50}%`}" :class="{yellowBackground:list.orderState == '5' || list.orderState == '8',greenBackground:list.orderState == '2' || list.orderState == '7' || list.orderState == '6'}"></div>
						<div :class="{yellowBorder:list.orderState == '5' || list.orderState == '8',greenBorder:list.orderState == '2' || list.orderState == '6' || list.orderState == '7'}"></div>
					</div>
					<div class="orderDetailsBoxTopCircle" v-if="list.IsBreaker == true">
						<icon name="cross" class="active"></icon>
						<div :style="'width: 100%'" style="background-color: #EB4948;"></div>
						<div style="border:2px solid #EB4948;" ></div>
					</div>
					<!-- <span>{{list.OnCreatedToTime}}</span> -->
				</div>
				<div class="orderDetailsBoxSchedule">
					<div class="orderDetailsBoxScheduleContent" :class="{red:list.IsBreaker}">
						<div class="orderDetailsBoxScheduleActiveLine" v-if="!list.IsBreaker" :style="'width:'+list.currentScale+'%'" ></div>
						<div class="orderDetailsBoxScheduleActiveLine" v-else-if="list.IsBreaker" :style="'width:100%'"></div>
						<div class="circleAnimation" :style="'left:'+list.currentScale+'%'" v-if="!list.IsBreaker">
							<div class="one"></div>
							<div class="two"></div>
							<div class="three" v-if="list.orderState == '2'">{{$t('ProvideHelp')}}({{list.currentScale}}%)</div>
							<div class="three" v-else-if="list.orderState == '5' && list.income == false">{{$t('Earning')}}</div>
							<div class="three" v-else-if="list.orderState == '5' && list.income == true || list.orderState == '8'">{{$t('RevenueCompletion')}}</div>
							<div class="three" v-else-if="list.orderState == '6'">{{$t('GetHelp01')}}({{list.currentScale}}%)</div>
						</div>
						<div class="orderDetailsBoxScheduleStrat"><span>{{$t('Create')}}</span></div>
						<!-- <div class="orderDetailsBoxSchedulePay"><span>正在排队</span></div>
						<div class="orderDetailsBoxScheduleQueue"><span>收益</span></div>
						<div class="orderDetailsBoxScheduleIncome"><span>收益完成</span></div> -->
						<div class="orderDetailsBoxScheduleEnd" v-if="!list.IsBreaker" :style="{ justifyContent: $i18n.locale == 'zh' ? 'center' : 'flex-end' }"><span>{{$t('Complete')}}</span></div>
						<div class="orderDetailsBoxScheduleEnd" v-else-if="list.IsBreaker"><span>{{$t('Default')}}</span></div>
					</div>
					<div class="orderDetailsBoxCreateTime">
						<span>{{$toTime(list.OnCreated,'Y/M/D H:i')}}</span>
						<span v-if="list.orderState == '7'">{{$toTime(list.succesOrderTime,'Y/M/D H:i')}}</span>
					</div>
				</div>
				<!-- <div class="orderDetailsBoxState04" v-if="list.isInCountdowning">
					<Button @click="ApplyProfitingCountDown" v-ButClickLimit>{{$t('ApplicationCountdown')}}</Button>
					<p>已开启支付倒计时</p>
					<span @click="rule = true;ruleType = 2">{{$t('AccelerationRule')}}</span>
				</div> -->
				<div class="orderDetailsBoxState01" v-if="list.orderState == '2' && list.IsBreaker != true">
					<div v-if="list.OnCountDownState">
						<div>{{list.times.substr(0,1)}}</div>
						<div>{{list.times.substr(1,1)}}</div>
						<p>:</p>
						<div>{{list.times.substr(3,1)}}</div>
						<div>{{list.times.substr(4,1)}}</div>
						<p>:</p>
						<div>{{list.times.substr(6,1)}}</div>
						<div>{{list.times.substr(7,1)}}</div>
					</div>
					<Button @click="transferUSDT" v-ButClickLimit>{{$t('Payment')}}</Button>
					<span @click="rule = true;ruleType = 0">{{$t('PaymentRules')}}</span>
				</div>
				<div class="orderDetailsBoxState02" v-else-if="list.orderState == '5' && list.income == false">{{$t('TimeToCompletion')}} {{list.queueDay}}</div>
				<div class="orderDetailsBoxState03" v-else-if="list.orderState == '5' && list.income == true">
					<!-- <Button @click="Settlement">{{ $t('Settlement') }}</Button> -->
					<Button @click="pushProvideHelp = true">{{ $t('Settlement') }}</Button>
				</div>
				<div class="orderDetailsBoxState05" v-else-if="list.orderState == '8'">
					<!-- <Button @click="pushProvideHelp = true">{{$t('GetHelp01')}}</Button> -->
					<Button @click="DoConvertToConsumer">{{$t('GetHelp01')}}</Button>
					<span @click="rule = true;ruleType = 1">{{$t('GetHelpRules')}}</span>
				</div>
				<!-- <div class="orderDetailsBoxState04" v-else-if="list.orderState == '6'">
					<span>已开启支付倒计时</span>
				</div> -->
			</div>
			<div class="orderDetailsDataInfo">
				<div>
					<div class="circleBgbox">
						<vant-circle v-model="currentRate01" :rate="list.currentPaymentedAmountProp" :speed="100" :text="list.currentPaymentedAmountString" :stroke-width="120" color='#6E5EFF' ></vant-circle>
						<div class="circleBg"></div>
					</div>
					<span>{{list.totalAmount}}</span>
					<span>{{$t('ProvideHelp')}}</span>
				</div>
				<div>
					<div class="circleBgbox">
						<vant-circle v-model="currentRate02" :rate="list.currentIncome" :speed="100" :text="list.incomeTotal" :stroke-width="120" color='#FF9A52' ></vant-circle>
						<div class="circleBg"></div>
					</div>
					<span>{{list.incomeTotal}}</span>
					<span>{{$t('expectedProfits')}}</span>
				</div>
				<div>
					<div class="circleBgbox">
						<vant-circle v-model="currentRate03" :rate="list.getHelpProp" :speed="100" :text="this.list.getHelpedAmountString" :stroke-width="120" color='#22CED4'></vant-circle>
						<div class="circleBg"></div>
					</div>
					<span>{{list.getHelpedAmountString}}</span>
					<span>{{$t('GetHelp01')}}</span>
				</div>
			</div>
			<!-- <div class="orderDetailsTranctionRecord">
				<div class="myContractBoxTitle">
					<span>{{$t('recording')}}</span>
				</div>
				<div class="myContractBoxList">
					<not-record v-if="!Record.length" :text="NoTransactionHistory"></not-record>
					<div v-for="(item,index) in Record" :key="index">
						<img src="../../../assets/Log_HelpGet.png" v-if="item.type == 'Get'" />
						<img src="../../../assets/Log_HelpTo.png" v-else />
						<div class="myContractBoxListRight">
							<div>
								<span>{{item.name}}</span>
								<span>{{item.address.substr(0,8)}}......{{item.address.substr(-8,8)}}</span>
							</div>
							<div>
								<span :class="{red:item.type == 'To',green:item.type == 'Get'}">{{item.type == 'Get' ? '+' : '-'}}{{$fromWei(item.amount,'','Mwei')}} USDT</span>
							</div>
						</div>
					</div>
				</div>
			</div> -->
		</div>
		
		<authorization
		:show="authorizedAccess" 
		:click="authorizedAccessFun"
		:needAuthorization='authorizedAccessType ? list.RemainingAmount : list.ConsumeDT' 
		:tokenType="tokenType"
		:authorizationEnds="authorizationEnds"
		@authorizationEnds="end"
		></authorization>
		
		<overlay :show="pushProvideHelp" class="pushProvideHelp" @click="pushProvideHelp = false">
			<div @click.stop>
				<div>
					<span>{{$t('GetHelp01')}}</span>
					<span>{{list.incomeTotal}} USDT</span>
				</div>
				<div>
					<span>{{$t('RelatedOrder')}}</span>
					<span @click="App_Push('/provideHelp')" v-if="list.RelatedOrder === false">{{$t('ToFinish')}}</span>
					<span v-else>{{$t('Completed')}}</span>
				</div>
				<div>
					<span>{{$t('FuelConsumption')}}</span>
					<span>{{list.ConsumeDT}} MPC</span>
				</div>
				<span>{{$t('Available')}}：{{this.list.DT}} MPC</span>
				<Button @click="Settlement" :disabled="list.RelatedOrder === false" v-ButClickLimit>{{$t('Commit')}}</Button>
			</div>
		</overlay>
		
		<overlay :show="rule" class="rule" @click="rule = false">
			<div>
				<div class="ruleTop">					
					<div>
						<icon name="fail"></icon>
					</div>
					<span v-if="ruleType == 0">{{$t('PaymentRules')}}</span>
					<span v-else-if="ruleType == 1">{{$t('GetHelpRules')}}</span>
					<span v-else-if="ruleType == 2">{{$t('AccelerationRule')}}</span>
				</div>
				<div class="ruleBottom" v-if="ruleType == 0">
					<p class="marginBottom">{{$t('PaymentRulesText01')}}</p>
					<p>{{$t('PaymentRulesText02')}}</p>
					<p>{{$t('PaymentRulesText03')}}</p>
					<p>{{$t('PaymentRulesText04')}}</p>
					<p class="marginBottom">{{$t('PaymentRulesText05')}}</p>
					<p class="marginBottom">{{$t('PaymentRulesText06')}}</p>
					<p>{{$t('PaymentRulesText07')}}</p>
					<div></div>
				</div>
				<div class="ruleBottom" v-else-if="ruleType == 1">
					<p>{{$t('GetHelpRulesText01')}}</p>
					<p>{{$t('GetHelpRulesText02')}}</p>
					<p>{{$t('GetHelpRulesText03')}}</p>
					<!-- <p>{{$t('GetHelpRulesText04')}}。</p> -->
					<div></div>	
				</div>
				<div class="ruleBottom" v-else-if="ruleType == 2">
					<p>{{$t('AccelerationRuleText01')}}</p>
					<div></div>
				</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import { Icon , Rate , Button , Circle as vantCircle , Toast , Overlay } from 'vant';
	import navBar from '@/components/nav-bar.vue';
	// import goldBag from "@/components/gold-bag.vue"
	// import hourglass from "@/components/hourglass.vue"
	// import Swiper from 'swiper'
	// import 'swiper/css/swiper.min.css'
	// import 'swiper/js/swiper.min.js'
	// import QRCode from 'qrcode'
	import Order from "@/common/Order.json"
	import Authorization from '@/components/Authorization.vue'
	import notRecord from '@/components/not-record'
	export default {
		components:{ navBar , Icon , Rate , Button , vantCircle , Authorization , Overlay , notRecord },
		data () {
			return{
				title: '',
				NoTransactionHistory: this.$t('NoTransactionHistory'),
				order: '',
				tokenType: 'USDT',
				currentRate01: 0,
				currentRate02: 0,
				currentRate03: 0,
				list: {
					OnCreated: '',
					order: '',
					RelatedOrder: '',
					currentIncome: 0,
					getHelpProp: 0,
					getHelpedAmountString: '0',
					totalIncome: '0',
					currentScale: 0,
					times: '00:00:00:00',
					IsBreaker: '',
					getHelpedAmountTotal: 0,
					currentPaymentedAmountProp: 0,
					getHelpedAmountTotalString: 0,
					totalAmount: 0,
					incomeTotal: '0',
					currentPaymentedAmountString: '0',
					succesOrderTime: 0,
				},
				Record: [],
				times: '',
				authorizedAccess: false,
				authorizationEnds: false,
				authorizedAccessType: true,
				pushProvideHelp: false,
				rule: false,
				ruleType: 0,
				isComplete: '',
				orderContract: {}
			}
		},
		methods:{
			async init(callback) {
				clearInterval(this.times);
				this.$set(this.list, 'currentScale', 0);
				this.pushProvideHelp = false;

				const contractId = this.$route.query.order,
					  orderAddress = this.$contracts.OrderManager.address;
				this.list.orderContract = this.$contracts.OrderManager;
				// if (typeof callback === 'function') {
				// 	callback();
				// }

				const order = (await this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'orders(uint256)',
					parameter: [{ type: "uint256", value: contractId }]
				}).then()).constant_result[0].decode(
					['address','uint256', 'uint256', 'uint256', 'uint256', 'uint8', 'bool'],
					['owner','index', 'total', 'paymented', 'profix', 'states', 'consumed']
				);

				// 合约地址
				this.list.order = orderAddress;
				// 合约归属人
				this.$set(this.list, 'contractOwner', order.owner);
				// 状态
				this.$set(this.list, 'orderState',order.states.toString());
				// 订单号
				this.$set(this.list, 'contractId' , contractId);
				// usdt授权额度
				this.$set(this.list, 'USDTAuthorizedAccess', (await this.$tronWeb.call({
					contractAddress: this.$contracts.ERC20Token.address,
					method: 'allowance(address,address)',
					parameter: [{ type: "address", value: this.$address },{ type: "address", value: orderAddress }]
				})).constant_result[0].decode(['uint256']).toString().fromSun());

				// 订单时间
				const orderTimes = (await this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'orderTimes(uint256)',
					parameter: [{ type: "uint256", value: contractId }]
				}).then()).constant_result[0].decode(
					['uint256','uint256','uint256','uint256','uint256','uint256','uint256','uint256','uint256'],
					['OnCreated','OnPaymentFrist','OnPaymentSuccess','OnProfitingBegin','OnCountDownStart','OnCountDownEnd','OnConvertConsumer','OnUnfreezing','OnDone']
				);
				// 创建时间
				this.$set(this.list, 'OnCreated', orderTimes.OnCreated.toNumber());
				this.$set(this.list, 'OnCreatedToTime', this.$toTime(orderTimes.OnCreated.toNumber(), 'Y/M/D H:i'));
				// 倒计时开始时间
				this.$set(this.list, 'OnCountDownStart', orderTimes.OnCountDownStart.toNumber());
				// 倒计时结束时间
				this.$set(this.list, 'OnCountDownEnd', orderTimes.OnCountDownEnd.toNumber());
				// 完成订单时间
				this.$set(this.list, 'OnPaymentSuccess', orderTimes.OnPaymentSuccess.toNumber());
				// 最新块号时间
				let currentTime = await this.$tronWeb.getBlockTime();
				if(order.states == 2){					
					// 订单倒计时是否开启
					this.$set(this.list, 'OnCountDownState', currentTime > this.list.OnCountDownStart);
					// 是否违约
					this.$set(this.list, 'IsBreaker', currentTime > this.list.OnCountDownEnd);
				}else{
					this.$set(this.list, 'IsBreaker', false);
				}

				// 总量
				this.$set(this.list, 'totalAmount', order.total.toString().fromSun());
				// 已付款
				this.$set(this.list, 'currentPaymentedAmount', order.paymented.toString().fromSun())
				this.$set(this.list, 'currentPaymentedAmountString', this.list.currentPaymentedAmount.toString())
				// 还需要付款多少
				this.$set(this.list, 'RemainingAmount', this.list.totalAmount.toBN().minus(this.list.currentPaymentedAmount).toString());
				// 已付款的百分比
				this.$set(this.list,'currentPaymentedAmountProp', this.list.currentPaymentedAmount.toBN().multipliedBy(100).div(this.list.totalAmount).toNumber());
				// 是否完成订单
				this.isComplete = !['2', '4', '6'].includes(this.list.orderState);

				// 是否处于倒计时
				// this.$set(this.list,'isInCountdowning', await this.list.orderContract.isInCountdowning().call());

				if (this.list.orderState !== '2') {
					// 计算预期收益
					// const ProfitPropP1 = this.ProfitPropP1;
					// const ProfitPropTotalP2 = this.ProfitPropTotalP2;
					// const p1 = this.list.totalAmount.toBN().multipliedBy(this.WaitTime / 86400).multipliedBy(ProfitPropP1 * 100).toNumber() / 100;
					// const p2 = this.list.totalAmount.toBN().multipliedBy(ProfitPropTotalP2 * 100).toNumber() / 100;
					// const incomeTimeProp = Math.floor((orderTimes.OnCountDownEnd.toNumber() - orderTimes.OnPaymentSuccess.toNumber()) / 3600)
					// if(incomeTimeProp > 48){
					// 	var p1p2_Amount = p1 + p2
					// }else if(incomeTimeProp > 24 && incomeTimeProp < 48){
					// 	var p1p2_Amount = (p1 + p2) * 0.8
					// }else{
					// 	var p1p2_Amount = (p1 + p2) * 0.6
					// }
					this.$set(this.list, 'incomeTotal',  order.profix.toString().fromSun());

					//p2实际时间
					const p2Time = orderTimes.OnUnfreezing.toNumber();

					// 订单排队时间
					this.$set(this.list, 'queueDay', this.$toTime(p2Time, 'Y/M/D H:i'));
					// 当前收益占比(预期收益 / 当前收益)
					this.$set(this.list, 'currentIncome', 100);

					// 提供帮助后状态判断
					if (currentTime < p2Time) {
						this.$set(this.list, 'currentScale', 50);
						this.$set(this.list, 'income', false);
					} else {
						this.$set(this.list, 'income', true);
						if (['6', '7'].includes(this.list.orderState)) {
							// 可获得帮助总量
							this.$set(this.list, 'getHelpedAmountTotal', order.profix.toString().fromSun());

							// 当前获得帮助
							this.$set(this.list, 'getHelpedAmountString', order.profix.toString().fromSun());

							// 当前获得帮助占比
							this.$set(this.list, 'getHelpProp', 100);
							this.$set(this.list, 'currentScale', 100);

							// 崩盘时间
							if(this.list.orderState === '7'){
								this.$set(this.list, 'succesOrderTime', orderTimes.OnDone.toString());

								// 转换消费者的时间
								// const OnConvertConsumer = await this.$tronWeb.call({
								// 	contractAddress: orderAddress,
								// 	method: 'times(uint8)',
								// 	parameter: [{ type: 'uint8', value: 6 }]
								// }).then(res => res.constant_result[0].decode(['uint256']).toNumber());
								
								// this.$set(this.list, 'CrashTime', Math.floor((currentTime - OnConvertConsumer) / 3600));
								// const ApplyProfitingCountDown = await this.$tronWeb.call({
								// 	contractAddress: orderAddress,
								// 	method: 'ApplyProfitingCountDown()'
								// }).then(res => res.constant_result[0].decode(['bool', 'bool'], ['canApply', 'applyResult']));
								// this.$set(this.list, 'CrashTimeResult', ApplyProfitingCountDown.canApply && ApplyProfitingCountDown.applyResult);
							}
						} else {
							this.$set(this.list, 'currentScale', 100);
							this.$set(this.list, 'income', true);
							
							//当前拥有的DT
							this.$set(this.list, 'DT', (await this.$tronWeb.call({
								contractAddress:  this.$contracts.ERC777_1Token.address,
								method: 'balanceOf(address)',
								parameter: [{ type: 'address', value: this.$address }]
							}).then()).constant_result[0].decode(['uint256']).toNumber().fromSun());

							const WithdrawCostProp = await this.$tronWeb.call({
								contractAddress:  this.$contracts.OrderManager.address,
								method: 'costProp()'
							}).then(res => res.constant_result[0].decode(['uint256']).toString().fromSzabo());

							const USDTToDTProp = await this.$tronWeb.call({
								contractAddress:  this.$contracts.OrderManager.address,
								method: 'exchangeRateUSDToMC()'
							}).then(res => res.constant_result[0].decode(['uint256']).toNumber());

							this.$set(
								this.list,
								'ConsumeDT',
								this.list.incomeTotal.toSun().toBN()
								.multipliedBy(WithdrawCostProp)
								.multipliedBy(USDTToDTProp).toString().fromSun()
								// .multipliedBy(this.WithdrawCostProp)
								// .multipliedBy(this.USDTToDTProp).toString().fromSun()
							);

							// 关联单逻辑
							const total = (await this.$tronWeb.call({
								contractAddress: this.$contracts.OrderManager.address,
								method: 'selectOrderIds(address)',
								parameter: [
									{ type: "address", value: this.$address },
								],
							})).constant_result[0].decode(
								['uint256[]'],
								['orderIndexMapping']
							);
							
							if(total.orderIndexMapping[total.orderIndexMapping.length - 1].toNumber() == this.list.contractId){
								this.$set(this.list, 'RelatedOrder', false);
							}else{
								this.$set(this.list, 'RelatedOrder', true);

								// const newOrder = (await this.$tronWeb.call({
								// 	contractAddress: this.$contracts.OrderManager.address,
								// 	method: 'orders(uint256)',
								// 	issuerAddress: this.$address,
								// 	parameter: [{ type: "uint256", value: total.orderIndexMapping[total.orderIndexMapping.length - 1].toNumber() }]
								// }).then()).constant_result[0].decode(
								// 	['address','uint256', 'uint256', 'uint256', 'uint256', 'uint8', 'bool'],
								// 	['owner','index', 'total', 'paymented', 'profix', 'states', 'consumed']
								// );

								// const currentOrderTimeLimt = Math.floor(Math.floor(currentTime) / 86400) * 86400;
								// const totalOrderTimes = (await this.$tronWeb.call({
								// 	contractAddress: this.$contracts.OrderManager.address,
								// 	method: 'orderTimes(uint256)',
								// 	parameter: [{ type: "uint256", value: total.orderIndexMapping[total.orderIndexMapping.length - 1].toNumber() }]
								// }).then()).constant_result[0].decode(
								// 	['uint256','uint256','uint256','uint256','uint256','uint256','uint256','uint256','uint256'],
								// 	['OnCreated','OnPaymentFrist','OnPaymentSuccess','OnProfitingBegin','OnCountDownStart','OnCountDownEnd','OnConvertConsumer','OnUnfreezing','OnDone']
								// )
								
								// if(totalOrderTimes.OnCreated.toNumber() >= currentOrderTimeLimt && totalOrderTimes.OnCreated.toNumber() < currentOrderTimeLimt + 86400 && !newOrder.consumed){
								// 	this.$set(this.list, 'RelatedOrder', true)
								// }else{
								// 	this.$set(this.list, 'RelatedOrder', false);
								// }
							}
						}
					}
				} else if (this.list.orderState === '2') {
					// 已付款的百分比
					this.$set(this.list, 'currentScale', this.list.currentPaymentedAmount.toString().toBN().multipliedBy(100).div(this.list.totalAmount).toNumber());
					
					this.times = setInterval(() => {
						currentTime++;
						if(currentTime == 0 || currentTime > this.list.OnCountDownEnd){
							this.$set(this.list, 'times', '00:00:00:00');
							return clearInterval(this.times);
						}else{
							const timesDown = this.timeDown(this.list.OnCountDownEnd, currentTime);
							this.$set(this.list, 'times', timesDown);
						}
					}, 1000);
				}

				// this.$tronWeb.call({
				// 	contractAddress: this.$contracts.Relations.address,
				// 	method: 'getForefathersLine(address,uint256)',
				// 	parameter: [
				// 		{ type: "address", value: this.$address },
				// 		{ type: "uint256", value: 15 },
				// 	],
				// }).then(res => {
				// 	console.log('getForefathers', res.constant_result[0].decode(['address[]', 'uint8[]'], ['a', 'b']))
				// })
				// this.$contracts.OrderManager.validCountOf(this.$address).call().then(res => {
				// 	console.log('validCountOf', res)
				// })
			},
			transferUSDT() {// 转USDT进合约
				if(Number(this.list.USDTAuthorizedAccess) < Number(this.list.RemainingAmount)) {
					return this.authorizedAccess = true;
				}
				Toast.loading({
					message: '正在交易中...',
					forbidClick: true,
					duration: 0
				});

				this.$tronWeb.call({
					contractAddress: this.list.order,
					method: 'orderPayment(uint256,uint256)',
					parameter: [
						{ type: 'uint256', value: this.list.contractId },
						{ type: 'uint256', value: this.list.RemainingAmount.toSun() }
					]
				}).then(async res => {

					this.list.orderContract.orderPayment(this.list.contractId, this.list.RemainingAmount.toSun())
					.send({ shouldPollResponse: true, feeLimit: '100'.toSun() }).then(res => {
						Toast.clear();
						this.init();
					})

				}).catch(err => {
					Toast.clear();
					console.log(err);
				});
			},
			updateState() {// 更新状态
				if(this.list.usdt.toBN().gte(this.list.totalAmount)){
					Toast.loading({
						message: '正在交易中...',
						forbidClick: true,
						duration: 0
					});

					this.list.orderContract.OrderStateCheck()
					.send({ feeLimit: '50'.toSun() }).then(hex => {
						sessionStorage.setItem('transactionHex', hex);

		                const interval = setInterval(() => {
		                    window.tronWeb.trx.getTransaction(hex).then(transaction => {
		                        if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
		                            sessionStorage.removeItem('transactionHex');
		                            clearInterval(interval);

									Toast.clear();
									this.init();
		                        }
		                    })
		                }, 2000);

					}).catch(err => {
						Toast.clear();
						console.log(err);
					});
				}
			},
			async DoConvertToConsumer() {// 转换状态-获得帮助
				if(this.list.orderState == '8'){
					Toast.loading({
						message: '正在交易中...',
						forbidClick: true,
						duration: 0
					});

					this.$tronWeb.call({
						contractAddress: this.list.order,
						method: 'orderWithdraw(uint256)',
						parameter: [{ type: "uint256", value: this.list.contractId }]
					}).then(async res => {
						if(!res.constant_result[0].decode(['bool'])){
							Toast.clear();
							return Toast.fail('系统暂时无法支付当前订单！');
						}

						this.list.orderContract.orderWithdraw(this.list.contractId)
						.send({ feeLimit: '100'.toSun() }).then(hex => {
							sessionStorage.setItem('transactionHex', hex);

			                const interval = setInterval(() => {
			                    window.tronWeb.trx.getTransaction(hex).then(transaction => {
			                        if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
			                            sessionStorage.removeItem('transactionHex');
			                            clearInterval(interval);

										Toast.clear();
										this.init();
			                        }
			                    })
			                }, 2000);
						}).catch(err => {
							Toast.clear();
							console.log(err);
						});
						
					}).catch(err => {
						Toast.clear();
						console.log(err);
					});
				}
			},
			ApplyProfitingCountDown(){// 申请开始支付倒计时
				Toast.loading({
					message: '正在交易中...',
					forbidClick: true,
					duration: 0
				});

				this.list.orderContract.ApplyProfitingCountDown()
				.send({ feeLimit: '50'.toSun() }).then(hex => {
					sessionStorage.setItem('transactionHex', hex);

	                const interval = setInterval(() => {
	                    window.tronWeb.trx.getTransaction(hex).then(transaction => {
	                        if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
	                            sessionStorage.removeItem('transactionHex');
	                            clearInterval(interval);

								Toast.clear();
								this.init();
	                        }
	                    })
	                }, 2000);

				}).catch(err => {
					Toast.clear();
					console.log(err);
				});
			},
			timeDown(endDateStr,currentTime){
				function add(m){return m<10?'0'+m:m }
				//结束时间
				var endDate = endDateStr;
				//当前时间
				var nowDate = currentTime;
				//相差的总秒数
				var totalSeconds = parseInt((endDate - nowDate))
				//天数
				var day=parseInt(totalSeconds/3600/24);
				//取模（余数）
				var modulo = totalSeconds % (60 * 60 * 24);
					
				//小时数
				var hours = Math.floor((modulo / (60 * 60)));
				modulo = modulo % (60 * 60);
				//分钟
				var minutes = Math.floor(modulo / 60);
				//秒
				var seconds = modulo % 60;
				return add(hours+(day*24)) + ":" + add(minutes) + ":" + add(seconds);
			},
			authorizedAccessFun(){
				Toast.loading({
					message: '正在交易中...',
					forbidClick: true,
					duration: 0
				});
				
				if(!this.authorizedAccessType){
					this.$contracts.ERC777_1Token.approve(this.list.order, this.list.ConsumeDT.toSun())
					.send({ feeLimit: '50'.toSun() }).then(hex => {
						sessionStorage.setItem('transactionHex', hex);

		                const interval = setInterval(() => {
		                    window.tronWeb.trx.getTransaction(hex).then(transaction => {
		                        if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
		                            sessionStorage.removeItem('transactionHex');
		                            clearInterval(interval);

									this.authorizedAccess = false;
									this.authorizationEnds = true;
									// this.$set(this.list, 'USDTAuthorizedAccess', (await this.$contracts.ERC20Token.allowance(this.$address, this.list.order).call()).toString().fromSun());
									this.DoConvertToConsumer();
		                        }
		                    })
		                }, 2000);
					}).catch(err => {
						Toast.clear();
						console.log(err);
					});
				}
				this.$contracts.ERC20Token.approve(this.list.order, this.list.RemainingAmount.toSun())
				.send({ feeLimit: '50'.toSun() }).then(hex => {
					sessionStorage.setItem('transactionHex', hex);

	                const interval = setInterval(() => {
	                    window.tronWeb.trx.getTransaction(hex).then(async transaction => {
	                        if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
	                            sessionStorage.removeItem('transactionHex');
	                            clearInterval(interval);

								this.$tronWeb.call({
									contractAddress: this.$contracts.ERC20Token.address,
									method: 'allowance(address,address)',
									parameter: [
										{ type: "address", value: this.$address },
										{ type: "address", value: this.list.order }
									]
								}).then(res => {
									Toast.clear();
									this.authorizedAccess = false;
									this.authorizationEnds = true;
									this.$set(this.list, 'USDTAuthorizedAccess', res.constant_result[0].decode(['uint256']).toString().fromSun());
									this.transferUSDT();
								})
	                        }
	                    })
	                }, 2000);
				}).catch(err => {
					Toast.clear();
					console.log(err);
				});
			},
			end(){
				this.authorizedAccess = false;
			},
			Settlement(){
				if(!this.list.RelatedOrder){
					return Toast.fail('请完成关联单')
				} else if(Number(this.list.DT) < Number(this.list.ConsumeDT)){
					return this.$toast.fail('MPC余额不足,需要'+this.list.ConsumeDT+'MPC');
				}
				Toast.loading({
					message: '正在交易中...',
					forbidClick: true,
					duration: 0
				});

				this.$tronWeb.call({
					contractAddress: this.list.order,
					method: 'orderSettlement(uint256)',
					parameter: [{ type: "uint256", value: this.list.contractId }]
				}).then(async Settlement => {
					this.list.orderContract.orderSettlement(this.list.contractId)
					.send({ feeLimit: '100'.toSun() }).then(hex => {
						sessionStorage.setItem('transactionHex', hex);

		                const interval = setInterval(() => {
		                    window.tronWeb.trx.getTransaction(hex).then(transaction => {
		                        if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
		                            sessionStorage.removeItem('transactionHex');
		                            clearInterval(interval);

									Toast.clear();
									this.init();
		                        }
		                    })
		                }, 2000);
					})
				}).catch(err => {
					Toast.clear();
					console.log(err);
				});
			}
		},
		async created() {
			this.$tronWeb && this.init(async () => {
				// const fromBlock = await this.$tronWeb.call({
				// 	contractAddress: this.list.order,
				// 	method: 'blockRange(uint256)',
				// 	parameter: [{ type: 'uint256', value: 0 }]
				// }).then(res => res.constant_result[0].decode(['uint256']).toString());
				
				// this.$tronWeb.event({
