<template>
	<div id="app" v-if="toggleView">
		<router-view />
		<self-tabbar />
		
		<overlay :show="show" class="IsBreaker">
			<div @click.stop>
				<img src="./assets/logo.png" />
				<span>{{$t('AccountLocked')}}</span>
				<p>{{$t('blacklistText01')}}{{CONFIG_ResolveBreakerDTAmount}}MPC！</p>
				<div @click="ResolveBreaker" v-ButClickLimit>{{$t('blacklistText02')}}</div>
				<span @click="$router.push('/exchange')">{{$t('blacklistText03')}}</span>
			</div>
		</overlay>

		<overlay :show="pauseable && !death" class="pauseable">
			<div @click.stop>
				<div>
					<img src="./assets/boxYellow.png" />
					<img src="./assets/pauseable1.png" />
					<span>{{$t('SystemDraw')}}<br/>{{$t('PleaseWait')}}......</span>
				</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import Vue from 'vue'
	import { Overlay , Toast } from 'vant'
	import { TronWeb, Contracts } from '@/common/init'

	export default {
		components: {
			Overlay,
			'self-tabbar': () => import(/* webpackChunkName: "home" */ '@/components/self-tabbar')
		},
		data() {
			return {
				name: true,
				CONFIG_ResolveBreakerDTAmount: '',
				show: false,
				IsBreaker: false,
				DT: 0,
				pauseable: false,
				death: true,
				toggleView: true
			}
		},
		created() {
			if (localStorage.getItem('theme') === 'sun') {
				import('@/theme/theme-sun.scss')
			} else {
				localStorage.setItem('theme','moon')
				import('@/theme/theme-moon.scss')
			}
			
			let locale = localStorage.getItem('locale')
			if (locale) {
				this.$i18n.locale = locale
			}

			window.onload = () => {
				const timeout = setTimeout(() => {
					if (window.tronWeb.defaultAddress.base58) {
						this.init();
						clearTimeout(timeout);
					}
				}, 100);

				const transactionHex = sessionStorage.getItem('transactionHex');
				if (transactionHex) {
					Toast.loading({
						message: '正在交易中...',
						forbidClick: true,
						duration: 0
					});

					const interval = setInterval(() => {
						window.tronWeb.trx.getTransaction(transactionHex).then(transaction => {
							if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
								sessionStorage.removeItem('transactionHex');
								Toast.clear();
								clearInterval(interval);
							}
						})
					}, 2000);
				}
			}
		},
		methods:{
			async ResolveBreaker(){// 解除黑名单
				if(Number(this.DT) < Number(this.CONFIG_ResolveBreakerDTAmount)){
					return Toast.fail('MPC不足');
				}
				Toast.loading({
					message: '正在交易中...',
					forbidClick: true,
					duration: 0
				});

				this.$contracts.OrderManager.resolveBreaker().send({
					feeLimit: '50'.toSun()
				}).then(hex => {
					sessionStorage.setItem('transactionHex', hex);

					const interval = setInterval(() => {
						window.tronWeb.trx.getTransaction(hex).then(transaction => {
							if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
								sessionStorage.removeItem('transactionHex');
								clearInterval(interval);

								Toast.clear();
								this.show = false;
								this.IsBreaker = false;
							}
						})
					}, 2000);
				}).catch(err => {
					Toast.clear();
					console.log(err);
				});
			},
			init() {
				const tronWeb = new TronWeb();

				Contracts.init().then(contracts => {
					tronWeb.__proto__.getBlockTime = async () => {
						const timestampBN = await contracts.KTimeController.offsetTime().call();
						return parseInt(new Date().getTime() / 1000) + timestampBN.toNumber();
					}

					Vue.prototype.$tronWeb = tronWeb;
					Vue.prototype.$address = tronWeb.address;
					Vue.prototype.$contracts = contracts;

					// window.setDepositedUSDMaxLimitEverDay = () => {
					// 	contracts.OrderManager.setDepositedUSDMaxLimitEverDay(0).send({
					// 		shouldPollResponse: true,
					// 	}).then( res => {
					// 		console.log('设置成功');
					// 	}).catch(console.error)
					// }
					
					window.depositedUSDMaxLimit = () => {
						contracts.OrderManager.depositedUSDMaxLimit().call((err,result)=>{
							console.log(result)
						})
					}
					
					// window.setTime = (timestamp) => {
					// 	contracts.KTimeController.increaseTime(timestamp).send({
					// 		shouldPollResponse: true
					// 	}).then(res => {
					// 		console.log('调整成功！');
					// 	}).catch(err => {
					// 		console.log('调整失败！');
					// 	})
					// }
					
					// window.usdt = (to,amount) => {
					// 	contracts.ERC20Token.transfer(to,amount.toSun()).send({
					// 		shouldPollResponse: true
					// 	}).then( res => {
					// 		console.log('转账成功')
					// 	}).catch(console.error)
					// }

					// window.trx = (to,amount) => {
					// 	tronWeb.sendTransaction(to, amount.toSun())
					// 	.then(res => {
					// 		console.log('转账成功');
					// 	}).catch(console.error)
					// }
					
					// window.changeProp = (num1,num2) => {
					// 	contracts.Swap.OwnerSetChangeQuota(num1.toSun(),num2.toSun()).send({
					// 		shouldPollResponse: true
					// 	}).then( res => {
					// 		console.log('修改成功');
					// 	}).catch(console.error)
					// }

					// window.DoBounds = () => {
					// 	tronWeb.call({
					// 		contractAddress: contracts.Noder.address,
					// 		method: 'doBounds()'
					// 	}).then(async res => {
					// 		await contracts.Noder.doBounds().send({ shouldPollResponse: true })
					// 		console.log('成功');
					// 	}).catch(console.error)
						
					// }

					// window.kaijiang = () => {
					// 	contracts.Pool.availTotalAmouns(0).call((err,result)=>{
					// 		contracts.OrderManager.distributeAwards().send({
					// 			shouldPollResponse: true,
					// 		}).then( res => {
					// 			console.log(res)
					// 			console.log('开奖成功');
					// 		}).catch(console.error)
					// 	})
					// }

					// window.getMC = (address, amount) => {
					// 	contracts.ERC777_1Token.operatorSend(contracts.ERC777_1Token.address, address, amount.toSun(),'0x00','0x00')
					// 	.send({ shouldPollResponse: true }).then(res => {
					// 		console.log('转账成功');
					// 	}).catch(console.error)
					// }

					this.changeView();
				});
			},
			async getData() {
				let currentTime = await this.$tronWeb.getBlockTime();

				Promise.all([
					this.$tronWeb.call({
						contractAddress: this.$contracts.OrderManager.address,
						method: 'isBreaker(address)',
						parameter: [{ type: "address", value: this.$address }]
					}),// 是否黑名单
					this.$tronWeb.call({
						contractAddress: this.$contracts.OrderManager.address,
						method: 'deadlineTime()'
					}),// 是否暂停
					this.$contracts.OrderManager.death().call()
				]).then(([ isBreaker, pauseable, death ]) => {
					this.IsBreaker = isBreaker.constant_result[0].decode(['bool']);
					const Pauseable = pauseable.constant_result[0].decode(['uint256']).toNumber();
					this.pauseable = (Boolean(Pauseable) && currentTime > Pauseable);
					this.death = death;

					if (this.IsBreaker) {
						this.$tronWeb.call({
							contractAddress: this.$contracts.ERC777_1Token.address,
							method: 'balanceOf(address)',
							parameter: [{ type: 'address', value: this.$address }]
						}).then(balance => {
							this.CONFIG_ResolveBreakerDTAmount = this.ResolveBreakerDTAmount.fromSun();
							this.DT = balance.constant_result[0].decode(['uint256']).toString().fromSun();

							if (this.$route.name == 'exchange') {
								this.show = false;
							} else {
								this.show = true;
							}
						})
					} else {
						this.show = false;
					}
				})
			},
			changeView() {
				this.getData();
				this.toggleView = false;

				this.$nextTick(() => {
					this.toggleView = true;
				});
			}
		},
		watch:{
			async $route(){
				if(this.IsBreaker){					
					if(this.$route.name == 'exchange'){
						this.show = false;
					}else{
						this.show = true;
					}
				}else{
					this.show = false;
				}
			}
		},
	}
</script>
<style lang="scss" scoped>
	.IsBreaker{
		z-index: 3!important;
		padding-top: 222px;
		
		&>div{
			width: 678px;
			// height: 672px;
			margin: 0 auto;
			border-radius: 20px;
			display: flex;
			flex-direction: column;
			align-items: center;
			padding: 42px 44px;
			
			&>img{
				width: 228px;
			}
			
			&>span:nth-child(2){
				font-size: 50px;
				color: #EB4948;
				margin: 28px 0 18px;
			}
			
			&>span:nth-child(5){
				font-size: 30px;
				color: #21C17A;
				margin: 28px 0 18px;
				text-decoration: underline;
			}
			
			&>p{
				color: #656363;
				font-size: 28px;
				text-align: left;
			}
			
			&>div{
				color: #fafafa;
				font-size: 40px;
				width: 261px;
				height: 83px;
				border-radius: 58px;
				border: 2px solid #FFEA50;
				line-height: 83px;
				text-align: center;
				margin-top: 82px;
				background-color: #18A664;
			}
		}
	}
	
	.IsBreaker{
		background-color: #FCF9EC;
	}
	
	.pauseable{
		&>div{
			position: relative;
			width: 100%;
			height: 100%;
			font-size: 50px;
			color: #FFEA50;
			text-align: left;
			
			&>div{
				position: absolute;
				top: 30%;
				left: 50%;
				transform: translate(-50%,-30%);
				
				&>img:nth-child(1){
					width: 751px;
					height: 752px;
					animation: myfirst 5s infinite;
					animation-timing-function: linear;
				}
				
				&>img:nth-child(2){
					width: 232px;
					height: 217px;
					position: absolute;
					left: 50%;
					top: 260px;
					transform: translateX(-50%);
				}
				
				@keyframes myfirst{
					100% {transform: rotate(360deg);}
				}
				
				&>span{
					position: absolute;
					left: 50%;
					transform: translateX(-50%);
					bottom: 80px;
				}
			}
		}
	}
	
	.guide{
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 22px;
		
		&>div{
			width: 100%;
			background-color: #2A405C;
			padding: 0 10px 0 16px;
			border-radius: 10px;
			
			&>.guideTitle{
				display: flex;
				align-items: center;
				color: #21C17A;
				font-size: 50px;
				padding: 26px 18px;
				border-bottom: 2px solid #18222F;
				
				&>span{
					&>span{
						color: #fff;
					}
				}
				
				&>img{
					width: 66px;
					height: 75px;
					margin-right: 26px;
				}
			}
			
			&>.guideContent{
				padding:46px 14px;
				color: #fff;
				font-size: 28px;
				
				&>.guideContentTitle{
					font-size: 32px;
					display: flex;
					align-items: center;
					margin-bottom: 10px;
					text-align: left;
					
					&>div{
						background-color: #21C17A;
						width: 10px;
						height: 36px;
						margin-right: 16px;
					}
				}
				
				&>div:nth-child(2),&>div:nth-child(3){
					display: flex;
					justify-content: space-between;
					// padding: 0 20px;
					margin-bottom: 30px;
	
					&>div{
						display: flex;
						flex-direction: column;
						align-items: center;
						// justify-content: center;
						
						
						&>img{
							width: 110px;
							height: 110px;
						}
						
						// &>span{
						// 	text-align: left;
						// }
					}
					
					&>div:nth-child(2){
						flex: 1;
					}
					
					&>div:nth-child(1){
						width: 150px;
					}
					
					&>div:nth-child(3){
						width: 180px;
					}
				}
				
				&>div:nth-child(3){
					&>div:nth-child(1){
						padding-right: 10px;
					}
				}
			}
		}
	}
</style>
<style lang="scss">
	// @import '@/theme/theme-sun.scss';
	
	#app {
		font-family: 'Avenir', Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		// color: #2c3e50;
		// background-color: $bgColor;
		// background-color: #242A33;
		// padding-top: 60px;
	}
	.box {
		width: 100vw;
		height: 100vh;
		overflow-y: auto;
	}
	.navBox {
		width: 100vw;
		height: calc(100vh - 120px);
		overflow-y: auto;
		padding-top: 40px;
	}
	.van-tabbar-page {
		width: 100vw;
		height: calc(100vh - 50px);
		overflow-y: auto;
	}
	.van-pull-refresh{
		overflow: visible!important;
	}
</style>
